import React, { memo, useState } from 'react';
import lodash from 'lodash';
import CouponCard from '@betternet/shared/panel/components/shop-details/coupon-card';
import './offer-list.less';
import CouponTooltip from '@betternet/shared/panel/components/shop-details/coupon-tooltip/coupon-tooltip';
import { useCouponTutorial } from '@betternet/shared/hooks/safe-shoppiing-feature';

function OfferList({ offers = [], name, domain, gaPrefix, internalPrefix }) {
  const { isCouponsTutorialCompleted, onCouponsTutorialComplete } = useCouponTutorial();

  return (
    <section className='offer-list'>
      <div className='offer-list__title'>
        {offers.length} coupons found {name && `for ${name}`}
      </div>
      {(isCouponsTutorialCompleted === false && offers.length && (
        <CouponTooltip onClose={() => onCouponsTutorialComplete()} />
      )) ||
        undefined}
      {offers.map(coupon => (
        <CouponCard
          coupon={coupon}
          key={coupon.id}
          domain={domain}
          gaPrefix={gaPrefix}
          internalPrefix={internalPrefix}
        />
      ))}
    </section>
  );
}

export default memo(OfferList);
