import throttle from 'lodash.throttle';
import { sendContentMessage } from './utils';

export default class BaseSERP {
  /**
   * Main SERP injection template - shown above the URL
   * @name serpTemplate
   * @function
   * @param {Object} merchant
   * @callback {string} template
   */

  /**
   * Main SERP injection template - shown above the URL
   * @name serpArgs
   * @param {array} configs SERP selector configs
   * @param {string} className SERP injection wrapper classes names
   * @param {serpTemplate} template
   * @param {string} secondaryTemplate Secondary SERP injection template -
   * shown near additional search results
   * @param {number} serpReactionThreshold Threshold of observer reaction
   * for SERP injection checking to be throttled
   * @param {number} serpThrottleTimeout Timeout of throttled check SERP function
   * @return {object} args
   */

  /**
   * Create new SERP injections
   * @constructor
   * @param {serpArgs} args
   */
  constructor(args) {
    const {
      configs,
      className = 'serp-injection',
      template,
      secondaryTemplate,
      serpReactionThreshold = 1000,
      serpThrottleTimeout = 1000,
    } = args;
    if (!configs) {
      throw new Error('SERP configurations are required');
    }
    if (!template) {
      throw new Error('SERP-injection template is required');
    }
    this.observer = null;
    this.config = configs.find((i) => global.location.href.search(i.rx) !== -1);
    this.className = className;
    this.firstClassName = this.className.split(' ')[0];
    this.template = template;
    this.throttledSERPs = throttle(this.checkSERPs, serpThrottleTimeout);
    this.secondaryTemplate = secondaryTemplate;
    this.serpReactionThreshold = serpReactionThreshold;
    if (this.config) {
      this.init();
    }
  }

  /**
   * Create mutation observers and append SERPs on mutations
   */
  init() {
    this.counter = 0;
    this.observer = new MutationObserver((mutations) => {
      this.counter += 1;
      mutations.forEach(({ target, addedNodes }) => {
        if (addedNodes[0] && addedNodes[0].className === this.firstClassName) {
          return;
        }
        // set throttle if there are too many mutations on the page
        if (this.counter > this.serpReactionThreshold) {
          this.throttledSERPs(target);
        } else {
          this.checkSERPs(target);
        }
      });
    });
    this.observer.observe(document.body, {
      attributes: false,
      childList: true,
      subtree: true,
      characterData: false,
    });
  }

  /**
   * Create SERP injection element and add listener
   * @param {Object} merchant Merhcant for current SERP for template creation
   * @return {HTMLElement} SERP injection element
   */
  createSERP(merchant) {
    const el = document.createElement('span');
    el.className = this.className;
    el.innerHTML = this.template(merchant);
    el.addEventListener('click', (e) => {
      e.stopPropagation();
      e.preventDefault();
      this.constructor.onClick(el);
    });
    return el;
  }

  /**
   * SERP onClick event
   * @param {HTMLElement} el SERP injection element
   */
  static onClick(el) {
    el.nextSibling.click();
  }

  /**
   * Append SERP injections for additional search results
   * @param {HTMLElement} link
   * @return {boolean}
   */
  appendSecondarySERPs(link) {
    const block = link.closest(this.config.blocks);
    if (!block) {
      return false;
    }
    const secondaryLinks = block.querySelectorAll(this.config.secondaryLinks);
    secondaryLinks.forEach((sLink) => {
      sLink.insertAdjacentHTML('beforeend', this.secondaryTemplate);
    });
    return true;
  }

  /**
   * Set container for the SERP injection
   * @param {HTMLElement} link website URL DOM element
   * @return {HTMLElement} Container for the SERP injection.
   */
  serpContainer(link) {
    return this.config.domain.indexOf('google') === 0
      ? link.parentNode.parentNode.parentNode
      : link.parentNode.parentNode;
  }

  /**
   * Append element into the page
   * @param {HTMLElement} container Container, where the SERP injection should be injected
   * @param {HTMLElement} serp SERP injection element
   */
  static appendSERP(container, serp) {
    container.prepend(serp);
  }

  /**
   * check and append SERP injections for needed elements
   * @param {HTMLElement} el Mutation target
   * @return {array} Merchants list for the links
   */
  checkSERPs(el) {
    if (!this.config) {
      return false;
    }
    const promises = [];
    el.querySelectorAll(this.config.links).forEach((link) => {
      const container = this.serpContainer(link);
      if (container.hasAttribute(this.firstClassName)) {
        return true;
      }
      if (container.querySelector(`.${this.firstClassName}`)) {
        return true;
      }
      container.setAttribute(this.firstClassName, true);
      const p = sendContentMessage({
        action: 'getMerchant',
        data: {
          url: link.href,
        },
      }).then((merchant) => {
        if (!merchant) {
          return false;
        }
        this.constructor.appendSERP(container, this.createSERP(merchant));
        if (this.secondaryTemplate) {
          this.appendSecondarySERPs(link);
        }
        return true;
      });
      return promises.push(p);
    });
    return Promise.all(promises);
  }
}
