import React, { useCallback, useEffect, useMemo, useState } from 'react';
import Progress from '@betternet/shared/content/components/caa/caa-progress';
import Notification from '@betternet/shared/content/components/caa/caa-notification';
import Introduction from '@betternet/shared/content/components/caa/caa-introduction';
import CouponApplier from '@softomate/caa-module';
import { useSessionStorage } from 'react-use';
import { sendContentMessage } from '@softomate/rework-party';
import { useAcceptSafeShoppingFeature } from '@betternet/shared/hooks/safe-shoppiing-feature';
import { sendContentInternalAnalytics } from '@common-shared/Reporting/reporting-methods';

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
  if (request.message === 'get_caa_status') sendResponse({ status: global.caaModule?.status });
  if (request.message === 'start_caa') global.caaModule?.process();
});

function CAAController({ configs, coupons, onNoConfig }) {
  const [closeCount, setCloseCount] = useSessionStorage('betternent_caa_close', 0);
  const { isShoppingFeatureAccepted, onShoppingFeatureAccept } = useAcceptSafeShoppingFeature();

  const [showProgress, toggleProgress] = useState(false);
  const [showNotification, toggleNotification] = useState(false);
  const [showIntroduction, toggleIntroduction] = useState(false);

  const [caa, setCAA] = useState(null);
  const [currentCoupon, setCurrentCoupon] = useState(null);
  const [status, setStatus] = useState(null);
  const [processIndex, setProcessIndex] = useState(null);
  const [total, setTotal] = useState(null);
  const [result, setResult] = useState(null);
  const maxShowCount = useMemo(() => {
    return /(.*\.)?ebay\..*/.test(window.location.host) ? 2 : 3;
  }, []);

  useEffect(() => {
    if (caa) {
      return;
    }
    if (coupons?.length > 0 && configs?.length > 0) {
      const caaModule = new CouponApplier({ configs, coupons, id: 'betternet' });
      global.caaModule = caaModule;
      setCAA(caaModule);
      if (caaModule.config) {
        caaModule.init();
      } else {
        onNoConfig?.(true);
      }
    }
  }, [coupons, configs, closeCount]);

  useEffect(() => {
    if (!caa) {
      return;
    }
    caa.on('update:status', status => {
      setStatus(status);
    });
    caa.on('update:processIndex', processIndex => {
      setProcessIndex(processIndex);
    });
    caa.on('update:total', total => setTotal(total));
    caa.on('update:currentCoupon', currentCoupon => setCurrentCoupon(currentCoupon));
    caa.on('update:result', result => setResult(result));
  }, [caa]);

  const apply = useCallback(() => {
    if (!caa) {
      return;
    }
    caa.process();
    toggleNotification(false);
    sendContentMessage({ method: 'coupons.activations.activationMessageHandler' });
  }, [caa]);

  useEffect(() => {
    if (isShoppingFeatureAccepted === false) {
      toggleIntroduction(status === 'init');
    } else if (isShoppingFeatureAccepted === true) {
      toggleNotification(status === 'init');
    }
    toggleProgress(['success', 'fail', 'process'].includes(status));
    if (status === 'success') {
      sendContentInternalAnalytics('ui_view', { view_name: 'ntf_discount_success' });
    } else if (status === 'fail') {
      sendContentInternalAnalytics('ui_view', { view_name: 'ntf_discount_fail' });
    } else if (status === 'init' && isShoppingFeatureAccepted) {
      sendContentInternalAnalytics('ui_view', { view_name: 'ntf_discount_show' });
    } else if (status === 'process') {
      sendContentInternalAnalytics('ui_view', { view_name: 'ntf_discount_process' });
    }
  }, [status, isShoppingFeatureAccepted]);

  const couponsCount = coupons?.length ?? 0;
  let savings = result?.savings;
  if (savings && Math.ceil(savings) !== savings) {
    savings = savings.toFixed(2);
  }

  const onNotificationClose = () => {
    setCloseCount(closeCount + 1);
    toggleNotification(false);
  };

  const onIntroductionClose = () => {
    setCloseCount(closeCount + 1);
    toggleIntroduction(false);
  };

  const onIntroductionAccept = () => {
    onShoppingFeatureAccept();
    toggleNotification(true);
    toggleIntroduction(false);
  };

  const stopProgress = () => {
    caa?.stop();
    toggleProgress(false);
    if (status === 'success') {
      sendContentInternalAnalytics('ui_click', { view_name: 'ntf_discount_success', action_name: 'btn_close' });
    } else if (status === 'fail') {
      sendContentInternalAnalytics('ui_click', { view_name: 'ntf_discount_fail', action_name: 'btn_close' });
    }
  };

  return (
    <>
      {showProgress && (
        <Progress
          close={stopProgress}
          count={couponsCount}
          index={processIndex}
          isSuccess={status === 'success'}
          isFail={status === 'fail'}
          isProgress={status === 'process'}
          willSave={savings}
        />
      )}
      {showNotification && closeCount < maxShowCount && (
        <Notification close={onNotificationClose} apply={apply} count={couponsCount} domain={document.domain} />
      )}
      {showIntroduction && closeCount < maxShowCount && (
        <Introduction apply={onIntroductionAccept} showLater={onIntroductionClose} domain={document.domain} />
      )}
    </>
  );
}

export default CAAController;
