import { getCouponFromPage, queryAll } from '../utils';
import { ApplyCoupon } from '../Steps';
import Base from './BaseObserver';
import Coupon from './Coupon';

/**
 * @overview
 * Page observer for merchants where coupons can be extracted
 * Might expose coupons
 * Provides a way to restore existing coupons
 * Intended to improve User Experience
 */

export default class AdvanceObserver extends Base {
  constructor(config, cache = {}) {
    super(config);
    const {
      coupons, total, state, actionState, couponIndex,
    } = cache;
    this.state = state || null;
    this.removeConfig = config.remove;
    this.total = total || 0;

    if (this.available) {
      const existingCoupon = this.getExistingCoupons();
      if (coupons) {
        this.coupons = coupons.map((couponCache) => new Coupon(couponCache, config.remove));
      } else {
        this.coupons = (existingCoupon && existingCoupon.length)
          ? existingCoupon
            .map(({ code, el }) => new Coupon({
              code,
              initial: true,
            }, config.remove, el))
          : [];
      }

      this.couponIndex = couponIndex || -1;
      this.cachedAction = actionState;
    }
  }

  /**
   * @override
   * get html element of a coupon and extracted coupon code
   *
   * @return {{
   *   code: string,
   *   el: HTMLHtmlElement,
   * }[]}
   */
  getExistingCoupons() {
    const coupon = this.config;
    return queryAll(coupon).map((el) => {
      try {
        const code = getCouponFromPage(coupon, el);
        if (code) {
          return { code, el };
        }
        return false;
      } catch (err) {
        return false;
      }
    }).filter((r) => r);
  }

  updateCouponInfo(updatedTotal) {
    const updatedCoupons = this.getExistingCoupons();
    const cachedCoupons = this.coupons.filter(({ removed }) => !removed);
    if (updatedCoupons.length < cachedCoupons.length) {
      // coupon was removed
      const removedCoupon = cachedCoupons
        // TODO: consider using another property to check coupons
        .filter((c) => !(c.hasOwnProperty('discount') && typeof c.discount === 'number'))
        .find(({ code }) => !updatedCoupons.find((c) => c.code === code));
      if (removedCoupon) {
        const detectedDiscount = cachedCoupons.reduce(
          (accumulator, { discount }) => (discount ? accumulator + discount : accumulator),
          0,
        );
        const totalDiff = updatedTotal - (this.total + detectedDiscount);
        const couponIndex = this.coupons
          .findIndex(({ code }) => code === removedCoupon.code);

        this.coupons[couponIndex].setDiscount(totalDiff);
      }
    } else {
      /* eslint-disable */
      // coupon was added
      if (updatedCoupons.length > this.coupons.length) {
        const addedCoupon = updatedCoupons
          .find(({ code }) => !this.coupons.find((c) => c.code === code));
        if (addedCoupon) {
          const totalDiff = this.total - updatedTotal;
          this.coupons.push({ ...addedCoupon, discount: totalDiff });
          this.total = updatedTotal;
        }
      }
      /* eslint-enable */
    }
  }

  /**
   * restore cached coupons
   *
   * @param applierConfig
   * @return {Promise<void>}
   */
  async restoreInitialCoupons(applierConfig) {
    if (this.available) {
      const cachedIndex = this.couponIndex;
      if (cachedIndex > -1) {
        const code = this.coupons[cachedIndex].code;
        const applier = new ApplyCoupon({
          ...applierConfig,
          code,
        });
        this.runningAction = applier;
        await applier.execute(this.cachedAction);
        this.runningAction = null;
      }

      // restore only coupons, that was removed by caa
      const validCoupons = this.coupons.filter(({ removed }) => !removed);

      const validIndex = cachedIndex > -1 ? validCoupons
        .findIndex((c) => c.code === this.coupons[cachedIndex].code) : -1;

      for (let i = validIndex > -1 ? validIndex : 0; i < validCoupons.length; i++) {
        this.couponIndex = this.coupons.findIndex((c) => c.code === validCoupons[i].code);
        const code = validCoupons[i].code;
        const applier = new ApplyCoupon({
          ...applierConfig,
          code,
        });
        this.runningAction = applier;
        await applier.execute();
        this.runningAction = null;
      }
    }
  }

  /**
   * update coupons info and emit events
   */
  observer() {
    const pageCoupons = this.getExistingCoupons();
    const cachedCoupons = this.coupons
      .filter(({ removed, couponNode }) => !removed && couponNode);
    const hasNewCoupon = pageCoupons
      .find(({ code }) => !cachedCoupons.find((el) => el.code === code));
    if (pageCoupons.length !== cachedCoupons.length || hasNewCoupon) {
      const removedElement = this.coupons
        .find(({ removed, code }) => !pageCoupons.find((c) => c.code === code) && !removed);
      if (removedElement) {
        // TODO: add discount saving
        removedElement.setRemovedState();
      }

      // detect rerendered coupons
      this.coupons.filter(({ couponNode, code }) => {
        const updatedCoupon = pageCoupons.find((c) => c.code === code);
        return updatedCoupon && updatedCoupon.el !== couponNode;
      }).forEach((coupon) => {
        const newNode = pageCoupons.find(({ code }) => code === coupon.code).el;
        coupon.resetObserveTarget(newNode);
      });

      // detect new coupon
      const newCoupon = pageCoupons.find(({ code }) => !this.coupons.find((c) => c.code === code));
      if (newCoupon) {
        const coupon = new Coupon({
          code: newCoupon.code,
          added: true,
        }, this.removeConfig, newCoupon.el);
        coupon.trackEvents();
        this.coupons.push(coupon);

        // https://imgflip.com/i/45o5mv
        this.emit('couponAdded', coupon.code);
      }
      const newCoupons = this.coupons.filter(({ removed }) => !removed);
      if (!cachedCoupons.length && newCoupons.length) {
        this.emit('hasAppliedCoupon', this.coupons.map(({ code }) => ({ code })));
      }
      if (cachedCoupons.length && !newCoupons.length) {
        this.emit('couponsCleared');
      }
    }
  }

  /**
   * serialize observer state
   * @return {{total: number, coupons: unknown[], couponIndex: number, state: null}|{}}
   */
  getState() {
    if (this.available) {
      const state = {
        state: this.state,
        total: this.total,
        coupons: this.coupons.map((c) => c.serialize()),
        couponIndex: this.couponIndex,
      };
      if (this.runningAction) {
        state.actionState = this.runningAction.getState();
      }
      return state;
    }
    return {};
  }
}
