import React, { useCallback, useState } from 'react';
import './coupon-card.less';
import { copyToClipBoard, sendContentMessage } from '@softomate/rework-party';
import { sendContentInternalAnalytics, sendContentReport } from '@common-shared/Reporting/reporting-methods';

function CouponCard({ coupon, gaPrefix = '', domain, internalPrefix }) {
  const [copied, setCopied] = useState(false);
  const copyCoupon = useCallback(() => {
    copyToClipBoard(coupon.promoCode);
    sendContentReport(`${gaPrefix}coupons_list`, 'copy', domain, coupon.promoCode);
    sendContentInternalAnalytics('ui_click', {
      view_name: `${internalPrefix}coupons`,
      action_name: 'btn_copy_coupon',
      action_detail: { website_name: domain }
    });
    setCopied(true);
    sendContentMessage({ method: 'coupons.activations.activationMessageHandler' });
    setTimeout(() => setCopied(false), 3000);
  }, [coupon, setCopied]);

  return (
    <article className='coupon-card' onClick={copyCoupon}>
      <header className='coupon-card__header'>
        <div className='coupon-card__coupon'>{coupon.promoCode}</div>
        {!copied && <div className='coupon-card__copy'>Copy</div>}
        {copied && <div className='coupon-card__copied'>Copied!</div>}
        <div className='coupon-card__expired'>{coupon.exp}</div>
      </header>
      <p className='coupon-card__description'>{coupon.name}</p>
    </article>
  );
}

export default React.memo(CouponCard);
