import merge from 'lodash.merge';
import { extractSubdomainFromUrl, getTabs, sendBackgroundMessage } from './utils';
import DynamicFetcher from './fetcher';

export default class BaseMerchants extends DynamicFetcher {
  constructor(args) {
    super(args);
    this.states = [];
    this.resetDelay = args.resetDeleay || 24 * 60 * 60 * 1000;
    this.resetIntervals = [];
  }

  update(force) {
    return super.update(force).then(this.updateStates.bind(this));
  }

  static defaultState() {
    return {
      activated: false,
      showNotification: true,
    };
  }

  updateStates() {
    this.data.forEach((m) => {
      if (!this.states.find((s) => s.id === m.id)) {
        this.states.push({ id: m.id, ...BaseMerchants.defaultState() });
      }
    });
  }

  // Use this method if user will be signed out
  refreshStates() {
    this.states.forEach((state) => {
      merge(state, BaseMerchants.defaultState());
    });
  }

  setState(args) {
    const {
      url, id, data, tabId,
    } = args;
    let sid = id;
    if (id === undefined || id === '' || id === null) {
      const merchant = this.getBy('url', url);
      sid = merchant && merchant.id;
    }
    if (sid === undefined || sid === '' || sid === null) {
      return false;
    }
    const state = this.states.find((s) => s.id === sid);
    merge(state, data);
    BaseMerchants.updateContentState({
      data: {
        merchant: { id: sid, ...state },
      },
      excludedTabId: tabId,
    });
    return state;
  }

  static updateContentState({ data, excludedTabId }) {
    return getTabs({}).then((tabs) => {
      const promises = [];
      const filtered = tabs.filter((tab) => tab.id !== excludedTabId);
      filtered.forEach((tab) => {
        promises.push(sendBackgroundMessage(tab.id, {
          action: 'silentUpdate',
          data,
        }));
      });
      return Promise.all(promises);
    });
  }

  setResetInterval({ id, tabId }) {
    let interval = this.resetIntervals.find((i) => i.id === id);
    if (!interval) {
      interval = { id };
      this.resetIntervals.push(interval);
    }
    clearInterval(interval.timer);
    interval.timer = setInterval(() => {
      this.setState({
        id,
        data: BaseMerchants.defaultState(),
        tabId,
      });
      clearInterval(interval.timer);
      this.resetIntervals = this.resetIntervals.filter((i) => i.id !== id);
    }, this.resetDelay);
  }

  removeResetInterval({ id }) {
    const interval = this.resetIntervals.find((i) => i.id === id);
    if (!interval) {
      return;
    }
    clearInterval(interval.timer);
    this.resetIntervals = this.resetIntervals.filter((i) => i.id !== id);
  }

  refreshResetIntervals() {
    this.resetIntervals.forEach((interval) => {
      clearInterval(interval.timer);
    });
    this.resetIntervals = [];
  }

  getBy(method, data) {
    switch (method) {
      case 'url': return this.getByUrl(data);
      case 'activationUrl': return this.getByActivationUrl(data);
      // case 'id': return this.getById(data); may be needed in future
      case 'name': return this.getByName(data);
      default: return null;
    }
  }

  getByUrl(url) {
    if (!url) {
      return null;
    }
    try {
      const host = extractSubdomainFromUrl(url);
      return this.data.find(({ domain }) => host.includes(`.${domain}`)
        || host.indexOf(domain) === 0);
    } catch (e) {
      return null;
    }
  }

  getByActivationUrl(url) {
    if (!url) {
      return null;
    }
    try {
      return this.data.find(({ activationUrl }) => url.includes(activationUrl));
    } catch (e) {
      return null;
    }
  }

  getByName({ name, value }) {
    return this.data.find((merchant) => merchant[name] === value);
  }
}
