import DynamicFetcher from './fetcher';
import { getStorageData, setStorageData, checkPermissions } from './utils';

export default class Notifications extends DynamicFetcher {
  constructor(args) {
    const {
      type, showInterval,
    } = args;
    super(args);
    this.type = type;
    this.showInterval = showInterval || 24 * 60 * 60 * 1000;
    this.checkInterval = this.showInterval / 10;
    this.notClickedNotifications = [];
  }

  async init() {
    const isEnabled = await checkPermissions({ permissions: ['notifications'] });
    if (!isEnabled) throw new Error('Permission is disabled');
    const items = await getStorageData([`${this.name}.lastShow`]);
    const lastShow = items[`${this.name}.lastShow`];
    if (!lastShow) {
      setStorageData({ [`${this.name}.lastShow`]: new Date().getTime() });
    }
    await this.update();
    this.setEvents();
    setInterval(() => {
      this.showNotification();
    }, this.checkInterval);
  }

  setEvents() {
    chrome.notifications.onClicked.addListener((target) => {
      const { url } = this.notClickedNotifications.find(({ id }) => id === target);
      chrome.tabs.create({ url });
      chrome.notifications.clear(target);
      this.notClickedNotifications = this.notClickedNotifications
        .filter((item) => item.id !== target);
    });
  }

  async showNotification() {
    if (this.data === null || this.data.length === 0) return;
    const items = await getStorageData([`${this.name}.lastShow`, `${this.name}.showedNotifications`]);
    const showedNotifications = items[`${this.name}.showedNotifications`] || [];
    const lastShow = items[`${this.name}.lastShow`];
    if (lastShow + this.showInterval > new Date().getTime()) return;
    if (Object.entries(showedNotifications).length === 0 || !showedNotifications) {
      setStorageData({ [`${this.name}.showedNotifications`]: [] });
    }
    const currentNotification = this.data.shift();
    const { id, programmation } = currentNotification;

    if (showedNotifications.includes(id)) {
      this.showNotification();
      return;
    }
    const endTime = new Date(programmation.end).getTime();
    const startTime = new Date(programmation.start).getTime();
    const currentTime = new Date().getTime();
    if (currentTime > endTime) {
      const newIds = [...showedNotifications, id];
      setStorageData({ [`${this.name}.showedNotifications`]: newIds });
      return;
    }
    if (currentTime < startTime) {
      this.data.push(currentNotification);
      return;
    }
    this.createNotification(currentNotification, showedNotifications);
  }

  createNotification(notification, showedNotifications) {
    const {
      id, text, description, img, background,
    } = notification;
    const options = {
      type: this.type,
      title: text,
      message: description,
      iconUrl: img,
    };
    if (this.type === 'image') options.imageUrl = background || img;
    chrome.notifications.create(id, options, () => {
      const newIds = [...showedNotifications, id];
      this.notClickedNotifications = [...this.notClickedNotifications, notification];
      setStorageData({
        [`${this.name}.showedNotifications`]: newIds,
        [`${this.name}.lastShow`]: new Date().getTime(),
      });
    });
  }
}
