import React from 'react';
import './button.less';
import classNames from 'classnames';

export default function Button({
  children,
  blue = false,
  lightGrey,
  fullWidth,
  onClick,
  className,
  transparent,
  spaceBetween,
  pointer = true
}) {
  return (
    <button
      className={classNames(
        'betternet_button',
        {
          'betternet_button--blue': blue,
          'betternet_button--transparent': transparent,
          'betternet_button--light-grey': lightGrey,
          'betternet_button--full-width': fullWidth,
          'betternet_button--space-between': spaceBetween,
          'betternet_button--pointer': pointer
        },
        className
      )}
      onClick={onClick}
    >
      {children}
    </button>
  );
}
