import React, { useEffect } from 'react';
import OfferList from '@betternet/shared/panel/components/shop-details/offer-list';
import BetternetLogo from '@betternet/shared/ui/components/Image/betternet-logo';
import CloseSmallCross from '@betternet/shared/ui/components/Image/close-small-cross';
import './content-coupons-list.less';
import { sendContentInternalAnalytics, sendContentReport } from '@common-shared/Reporting/reporting-methods';

const ContentCouponsList = ({ offers, close, domain }) => {
  useEffect(() => {
    sendContentReport('notification_coupons_list', 'show', window.location.hostname);
    sendContentInternalAnalytics('ui_view', { view_name: 'ntf_coupons' });
  }, []);

  const onClose = () => {
    sendContentReport('notification_coupons_list', 'close', window.location.hostname);
    close();
  };

  return (
    <div className='content-coupons-list'>
      <div className='content-coupons-list__header'>
        <div />
        <BetternetLogo />
        <CloseSmallCross onClick={onClose} />
      </div>
      <div className='content-coupons-list__body'>
        <OfferList offers={offers} domain={domain} gaPrefix='notification_' internalPrefix='ntf_' />
      </div>
      <div className='content-coupons-list__footer content-coupons-list__footer-text'>
        Your shopping protected by&nbsp;
        <a
          href='https://www.betternet.co/'
          target='_blank'
          rel='noopener noreferrer'
          className='content-coupons-list__footer-text'
        >
          Betternet
        </a>
      </div>
    </div>
  );
};

export default ContentCouponsList;
