import { queryFromNodeOrDoc } from '../utils';

/**
 * Save states of coupons on the page
 */
export default class Coupon {
  /**
   *
   * @param code {string}
   * @param removed {boolean}
   * @param initial {boolean}
   * @param added {boolean}
   * @param discount {number?}
   * @param removeRules {{submit: string?}|[{submit: string?}]?}
   * @param node {HTMLElement | HTMLHtmlElement}
   */
  constructor({
    code, removed, initial, added, discount,
  }, removeRules, node = document.documentElement) {
    this.code = code;
    this.isTracking = false;
    this.isTrusted = false;
    this.removed = removed;
    this.inital = initial;
    this.added = added;
    this.discount = discount;
    this.couponNode = node;
    if (removeRules) {
      const firstRule = Array.isArray(removeRules) ? removeRules[0] : removeRules;
      this.submitSelector = firstRule.submit;
      this.setObserveTarget(node);
    }
  }

  /**
   * @param node {HTMLElement?} - coupon node
   */
  setObserveTarget(node = this.couponNode) {
    if (this.submitSelector) {
      this.removeTrigger = queryFromNodeOrDoc(this.submitSelector, node);
    }
  }

  /**
   * start listening clicks on target.
   * might be useful in future to detect competitive CAA
   */
  trackEvents() {
    if (this.removeTrigger && !this.isTracking) {
      this.listener = ({ isTrusted }) => {
        // this isn't working. TODO: research it
        // might be useful to add a listener to document.documentElement
        // and check if 'target' is contained by removeTrigger node
        this.isTrusted = isTrusted;
      };
      this.removeTrigger.addEventListener('click', this.listener);
      this.removeTrigger.addEventListener('mousedown', this.listener);
      this.isTracking = true;
    }
  }

  stopEventsTracking() {
    if (this.removeTrigger && this.isTracking) {
      this.removeTrigger.removeEventListener('click', this.listener);
      this.removeTrigger.removeEventListener('mousedown', this.listener);
      this.isTracking = false;
    }
  }

  setState({ removed, added, initial }) {
    if (removed) {
      this.removed = true;
      this.added = false;
    }
    if (added) {
      this.removed = false;
      this.added = true;
    }
    if (initial) {
      this.inital = true;
    }
  }

  setRemovedState() {
    this.setState({ removed: true });
    this.stopEventsTracking();
  }

  setDiscount(discount) {
    this.discount = discount;
  }

  resetObserveTarget(node) {
    if (this.isTracking) {
      this.stopEventsTracking();
      this.couponNode = node;
      this.setObserveTarget(node);
      this.trackEvents();
    } else {
      this.couponNode = node;
      this.setObserveTarget(node);
    }
  }

  /**
   * prepare coupons for saving into session storage
   * @return {{code: string, removed: boolean, added: boolean, initial: *}}
   */
  serialize() {
    const status = {
      code: this.code,
      added: this.added,
      initial: this.initial,
      removed: this.removed,
    };
    if (this.hasOwnProperty('discount')) {
      status.discount = this.discount;
    }
    return status;
  }
}
