import {
  getJSONValue, getUrl, parseHtml, parseTotal, pause,
} from '../utils';
import { ActionStep } from '../enums';
import DefaultAction from './DefaultAction';

const {
  RUN, POSTRUN, RESULT,
} = ActionStep;

export default class RequestAction extends DefaultAction {
  constructor(args) {
    super(args);
    const {
      cacheResponse, total, config, cache,
    } = args;
    this.config = config;
    this.cacheResponse = cacheResponse;
    this.cache = cache;
    this.total = total;
  }

  async [RUN]() {
    const { action } = this;
    let url;
    try {
      url = getUrl(action.url);
    } catch (err) {
      // use initial url
    }

    const requestOptions = {
      method: action.type,
      body: action.body,
      headers: action.headers,
      dataType: action.response ? action.response.type : null,
    };

    if (this.stopped) {
      return false;
    }

    return RequestAction.smartFetch(url, requestOptions, action.xhr);
  }

  async [POSTRUN](request) {
    const { action } = this;

    if (action.postTimeout) {
      await pause(action.postTimeout);
    }

    if (action.response) {
      // TODO: Seems like very shitty implementation. might cause bugs. Discuss with QA.
      if (
        action.response.requireParams
        && action.response.requireParams.length > 0
      ) {
        const jsonResponse = await request.json();
        return RequestAction.validateResponse(action.response, jsonResponse);
      }

      if (this.stopped) {
        return false;
      }

      const totalUrl = action.response.url;

      let totalResponse;

      if (totalUrl) {
        totalResponse = totalUrl && await fetch(totalUrl);
      }

      return totalResponse || request;
    }

    return request;
  }

  async [RESULT](response) {
    if (this.action.response) {
      // TODO: fix respecting new rules class
      return parseTotal({
        coupon: { code: this.code },
        response,
        action: this.action,
        xhr: this.action.xhr,
        previousCouponPrice: this.previousTotal,
        total: this.total,
        config: this.config,
        cacheResponse: this.cacheResponse,
      });
    }
    if (this.cacheResponse) {
      const responseString = await response.text();
      return {
        htmlCache: parseHtml(responseString),
      };
    }
    return false;
  }

  /**
   * Perform request considering options and request type
   *
   * @param url {string} - request url
   * @param options {object|RequestInit} - request options
   * @param xhr {boolean} - flag to use XMLHttpRequest instead of fetch (preserve FormData body)
   *
   * @return {Promise<Response>|Promise<unknown>}
   */
  static smartFetch(url, options, xhr) {
    if (xhr) {
      return new Promise((resolve, reject) => {
        const request = new XMLHttpRequest();
        request.open(options.method, url, false);
        if (options.headers) {
          const headers = Object.entries(options.headers);
          headers.forEach(([key, value]) => {
            request.setRequestHeader(key, value);
          });
        }

        request.onload = (res) => resolve(res.currentTarget.responseText);
        request.onerror = (err) => reject(err);

        request.send(options.body || undefined);
      });
    }

    // copy url for further manipulations
    let requestUrl = `${url}`;
    const cookedOptions = { ...options };

    if (!['post', 'put'].includes(options.method.toLowerCase())) {
      if (options.body) {
        // TODO: check if should be join('&') after
        requestUrl += `?${Object.entries(options.body)
          .map(([key, value]) => value && `${key}=${value}`)
          .filter((el) => el)
          .join('&')
        }`;
      }

      delete cookedOptions.body;
    }
    if (cookedOptions.body && typeof cookedOptions.body === 'object') {
      cookedOptions.body = JSON.stringify(cookedOptions.body);
    }

    return fetch(requestUrl, cookedOptions);
  }

  /**
   * @param resp {ResponseConfig}
   * @param responseData {Object} - response json object
   *
   * @return boolean
   */
  static validateResponse(resp, responseData) {
    const { requireParams } = resp;
    if (!requireParams) {
      return true;
    }

    if (responseData) {
      /* eslint-disable */
      for (const param of requireParams) {
        const value = getJSONValue(responseData, param.path);
        if (value.toString() === param.value.toString()) {
          return true;
        }
      }
      /* eslint-enable */
    }

    return false;
  }
}
