import EventEmitter from 'events';
import {
  getSSItem,
  muteBlockers,
  pause,
  DOMContentLoaded,
  findConfig,
  waitFields,
  log,
  calculateBestResult,
  clearSessionStorageKeys,
  getSuccessCoupons,
  isRequestAction,
  getUpdatedPage,
  calculateCouponDiscount,
  getAmount,
  queryAll,
  getSelector,
} from './utils';

import {
  Status,
} from './enums';

import CouponStep, { ApplyCoupon, RemoveCoupon, Unlock } from './Steps';
import AppliedCoupons from './AppliedCoupons';
import InitialCoupons from './PageObserver';

let cachedUrl;
let isFirstInstance = true;
let cachedValidation;

export default class CouponApplier extends EventEmitter {
  /**
   * @param {{
   *    id: (number|string),
   *    savePreviousDiscount: boolean=,
   *    configs: [Object],
   *    coupons: [{code: string}],
   * }} options
   *    id: Uniq id to use for saving state into Session Storage
   *    savePreviousDiscount: flag to turn on/off discount saving after first coupon removal
   *    configs: Array of available config.
   *    coupons: Array of coupons to apply.
   */
  constructor(options = {}) {
    super();

    if (isFirstInstance) {
      isFirstInstance = false;
    } else {
      throw new Error('Only single instance of CAA is allowed');
    }

    if (!options.id) {
      throw new Error('\'id\' is required property for the first argument of the CAA constructor');
    }
    this.id = options.id;

    /* eslint-disable */
    for (const key of [
      'status',
      'result',
      'processIndex',
      'stepType',
    ]) {
      this.defineObservedProperty(key);
    }
    // this.defineObservedProperty('status'); // init, start, process, finish, success, fail, stop
    /* eslint-enable */

    this.stopReason = null;
    this.bestCoupon = null;
    this.disregardPreviousDiscount = !options.savePreviousDiscount;

    const coupons = [];

    // validate coupons and make it uniq
    /* eslint-disable */
    for (const coupon of options.coupons) {
      if (coupon.code && !coupons.find(({ code }) => coupon.code === code)) {
        coupons.push(coupon);
      }
    }
    /* eslint-enable */

    this.coupons = coupons;

    const setConfig = () => {
      // handle reactive sites
      if (!cachedUrl || cachedUrl !== location.href) {
        cachedUrl = location.href;

        this.config = findConfig(options.configs);
        if (!this.config) {
          this.stopReason = 'noConfig';
          this.status = Status.UNAVAILABLE;
          try {
            this.stopObserving();
          } catch (e) {
            // ignore
          }
        } else {
          const cache = getSSItem(`CAA-${this.id}`);
          const {
            status, timeStamp,
          } = cache || {};
          const isValid = cache
              && timeStamp
              && (timeStamp + 30000 > Date.now())
              && status;
          // TODO: refactor it. may be it is better to have some promise that will resolve on init.
          if (isValid || (this.stopReason && this.status)) {
            cachedValidation = isValid;
            this.init();
          }
          if (this.stopReason) this.stopReason = null;
          if (this.status && this.status !== Status.PROCESS) this.status = null;
        }
      }
    };
    setConfig();
    setInterval(setConfig, 3000);

    if (!this.config) {
      this.stopReason = 'noConfig';
      this.status = Status.STOP;
    }
  }

  async init() {
    try {
      await waitFields(this.config);
    } catch (err) {
      log('log', 'no controls in the config');
    }
    if (!this.status || this.stopReason) {
      await this.setInitState();
    }
  }

  async setInitState() {
    const cache = getSSItem(`CAA-${this.id}`);
    const {
      status,
      processIndex,
      total,
      appliedCoupons,
      stepType,
      result,
      bestCoupon,
      previousCouponPrice,
      previousDiscount,
      stackIndex,
      stackCoupons,
      initialState,
      runningAction,
    } = cache || {};

    const currentTotal = getAmount(this.config.controls.total);

    if (cachedValidation) {
      this.processIndex = processIndex;
      this.status = status;
      this.total = total;
      this.appliedCoupons = new AppliedCoupons(this.total, appliedCoupons);
      this.stepType = stepType;
      this.previousCouponPrice = previousCouponPrice;
      this.previousDiscount = previousDiscount;
      this.initialState = new InitialCoupons(this.config, initialState);
      if (status === Status.FINISHING) {
        this.result = result;
        this.bestCoupon = bestCoupon;
        await DOMContentLoaded();
        await this.unlock();
        if (this.config.stack) {
          // this looks like shit. TODO: Consider implementing some sane logic here.
          this.stackIndex = stackIndex + 1;
          this.stackCoupons = stackCoupons;
          this.applyStackCoupons(runningAction);
        } else {
          this.successProcess(runningAction);
        }
        this.clearStorage();
      }
      await DOMContentLoaded();
      // TODO: consider replacing this with the list of required nodes for validation
      if ([Status.STARTING, Status.PROCESS].includes(this.status)) {
        await this.unlock();
        this.process(runningAction);
      }
    } else {
      this.clearStorage();
      this.processIndex = 0;
      this.status = Status.INIT;
      this.total = currentTotal || 0;
      this.appliedCoupons = new AppliedCoupons(this.total);
      this.stepType = null;
      this.previousCouponPrice = 0;
      this.previousDiscount = 0;
      this.result = null;
      this.bestCoupon = null;
      this.stackIndex = null;
      this.initialState = new InitialCoupons(this.config);
      // TODO: emit event for existing coupons before initing
      this.startObserving();
    }
  }

  startObserving() {
    this.initialState.startObserving();
    ['hasAppliedCoupon', 'couponsCleared', 'couponAdded'].forEach((eventName) => {
      this.initialState.on(eventName, (...args) => {
        this.emit(eventName, ...args);
      });
    });
    if (this.initialState.coupons && this.initialState.coupons.length) {
      this.emit('hasAppliedCoupon', this.initialState.coupons.map(({ code }) => ({ code })));
    }
  }

  stopObserving() {
    if (this.initialState) {
      this.initialState.stopObserving();
      ['hasAppliedCoupon', 'couponsCleared', 'couponAdded'].forEach((eventName) => {
        this.initialState.removeAllListeners(eventName);
      });
    }
  }

  /**
   * clear session storage and reinit CAA
   */
  async reset() {
    if (!this.stopReason) {
      await this.stop();
      this.clearStorage();
      this.setInitState();
      return true;
    }
    return false;
  }

  clearStorage() {
    clearSessionStorageKeys(`CAA-${this.id}`);
  }

  saveState() {
    const {
      processIndex,
      status,
      total,
      appliedCoupons,
      coupons,
      config,
      stepType,
      result,
      bestCoupon,
      previousCouponPrice,
      previousDiscount,
      stackIndex,
      stackCoupons,
      initialState,
      runningAction,
    } = this;
    sessionStorage.setItem(`CAA-${this.id}`, JSON.stringify({
      processIndex,
      status,
      total,
      appliedCoupons: appliedCoupons.getState(),
      coupons,
      config,
      stepType,
      bestCoupon,
      runningAction: runningAction && runningAction.getState(),
      timeStamp: Date.now(),
      result,
      previousCouponPrice,
      previousDiscount,
      stackIndex,
      stackCoupons,
      initialState: initialState.getState(),
    }));
  }

  /**
   * Define a property to observe
   * @param name {string} - property name to observer
   */
  defineObservedProperty(name) {
    Object.defineProperty(this, name, {
      get: () => this[`_${name}`],
      set: (value) => {
        if (this[`_${name}`] !== value) {
          this[`_${name}`] = value;
          this.emit(`update:${name}`, value);
        }
      },
    });
  }

  /**
   * @private
   * @return {{
   *  previousCouponPrice: number,
   *  total: (boolean|*|number),
   *  code: *,
   *  previousDiscount: number,
   *  config: *,
   *  status: (Status|string|null)
   * }}
   */
  getSharedConfig() {
    return {
      code: this.coupons[this.processIndex] ? this.coupons[this.processIndex].code : '',
      config: this.config,
      status: this.status,
      previousCouponPrice: this.previousCouponPrice,
      total: this.total,
      previousDiscount: this.previousDiscount,
    };
  }

  /**
   * @private
   *
   * remove coupon
   *
   * @return {Promise<*>}
   */
  async removeCoupon() {
    const remover = new RemoveCoupon(this.getSharedConfig());

    this.runningAction = remover;
    const cache = await remover.execute();
    this.runningAction = null;

    return cache;
  }

  /**
   * @private
   *
   * @param processIndex
   * @param coupons
   * @param cache
   *
   * @return {Promise<boolean|*>}
   */
  async applyCoupon(processIndex = this.processIndex, coupons = this.coupons, cache) {
    const code = coupons[processIndex] ? coupons[processIndex].code : '';
    const couponApplier = new ApplyCoupon({
      ...this.getSharedConfig(),
      code,
    });

    this.runningAction = couponApplier;
    const result = await couponApplier.execute(cache);
    this.runningAction = null;

    return result;
  }

  /**
   * applying best coupon
   *
   * @param cache {*?} - cached state
   *
   * @return {Promise<void>}
   */
  successProcess(cache) {
    const successProcess = async () => {
      const save = () => {
        this.saveState();
      };

      window.addEventListener('unload', save);

      await this.isPaused;
      await this.applyCoupon(this.processIndex, this.coupons, cache);
      await this.isPaused;

      // TODO: implement cache
      this.status = Status.SUCCESS;
      await this.isPaused;
      window.removeEventListener('unload', save);
      this.clearStorage();
    };

    const process = successProcess();
    if (!this.isRunning) {
      this.isRunning = process;
      process.finally(() => {
        this.isRunning = false;
      });
    }

    return process;
  }

  /**
   * @private
   * Apply all successful coupons
   *
   * @param stackCoupons
   * @return {Promise<void>|Promise<unknown>|Promise<void>}
   */
  applyStackCoupons(stackCoupons = this.stackCoupons) {
    const applyCoupons = async (coupons) => {
      const save = () => {
        this.saveState();
      };

      window.addEventListener('unload', save);

      while (this.stackIndex < coupons.length) {
        this.processIndex = this.coupons
          .findIndex(({ code }) => coupons[this.stackIndex].code === code);
        this.stackIndex += 1;
        await this.isPaused;
        await this.applyCoupon();
        await this.isPaused;
      }

      let discount = 0;
      try {
        discount = getAmount(this.config.controls.discount) || 0;
      } catch (e) {
        // ignore
      }
      this.status = Status.SUCCESS;
      await this.isPaused;
      this.result = {
        savings: discount || (this.total - getAmount(this.config.controls.total)),
      };
      await this.isPaused;
      window.removeEventListener('unload', save);
      this.clearStorage();
    };

    const process = applyCoupons(stackCoupons);

    if (!this.isRunning) {
      this.isRunning = process;
      return process.finally(() => {
        this.isRunning = false;
      });
    }

    return process;
  }

  async unlock() {
    const unlock = new Unlock(this.getSharedConfig());
    await unlock.execute();
  }

  /**
   * @private
   *
   * Run before main process. Remove existing coupon and update total, if needed.
   * Mutes alerts and enables status caching
   *
   * @return {Promise<function(...[*]=)>} - function to restore alerts and disable status caching.
   */
  async preRun() {
    const restorePageBlockers = muteBlockers();
    const save = () => {
      this.saveState();
    };
    window.addEventListener('unload', save);

    let requestCache = null;
    if (this.status === Status.INIT) {
      this.status = Status.STARTING;
      await this.isPaused;
      await this.unlock();
      await this.isPaused;
      if (this.disregardPreviousDiscount
        && this.initialState.available
        && this.initialState.coupons
        && this.initialState.coupons.length) {
        const { coupon, total } = this.config.controls;

        this.initialState.setTotal(getAmount(total));
        // TODO: fix it. use class instead;
        if (this.config.stack && coupon) {
          // Remove stacking coupons
          const elements = queryAll(getSelector(coupon));
          /* eslint-disable */
          // will work only with clicks. TODO: add support for other types
          for (let i = 0; i < elements.length; i++) {
            const actionConfig = {
              ...this.getSharedConfig(),
              code: this.initialState.coupons[i].code || '',
            };
            const remover = new RemoveCoupon(actionConfig);
            await this.isPaused;
            requestCache = await remover.execute();
            await this.isPaused;

            const totalFromResponse = getAmount(total, requestCache);
            const totalFromPage = getAmount(total);
            this.initialState.updateCouponInfo(totalFromResponse || totalFromPage);
          }
          /* eslint-enable */
        } else {
          const actionConfig = {
            ...this.getSharedConfig(),
            code: this.initialState.coupons[0].code || '',
          };
          const remover = new RemoveCoupon(actionConfig);
          await this.isPaused;
          requestCache = await remover.execute();
          await this.isPaused;
        }
      }
    }

    if (this.status === Status.STARTING) {
      let parsedTotal = getAmount(this.config.controls.total);
      if (this.initialState) {
        this.initialState.discount = parsedTotal - this.initialState.total;
      }

      if (this.processIndex === 0 && this.disregardPreviousDiscount) {
        const removeRules = [].concat(this.config.remove);

        // TODO: might be slow. Consider adding a rule to control it
        if (
          this.config.remove
            && removeRules.find(({ type }) => isRequestAction(type) || type === 'submit')
            && requestCache
            && requestCache instanceof HTMLDocument
        ) {
          const totalFromResponse = getAmount(this.config.controls.total, requestCache);
          if (!totalFromResponse) {
            // TODO: fix it. add a way to use data from json.
            const updatedPage = await getUpdatedPage();
            const updatedTotal = getAmount(this.config.controls.total, updatedPage);
            if (updatedTotal) {
              parsedTotal = updatedTotal;
            }
          } else {
            parsedTotal = totalFromResponse;
          }
        }

        // update total before executing
        // TODO: handle reactive pages if price is loaded after dom load.
        this.total = parsedTotal;
      }
    }

    return () => {
      try {
        restorePageBlockers();
      } catch (err) {
        log('error', 'error restoring blockers', err);
      }
      window.removeEventListener('unload', save);
    };
  }

  async couponStep(cache) {
    const newCoupon = new CouponStep({
      code: this.coupons[this.processIndex].code,
      config: this.config,
      status: this.status,
      previousCouponPrice: this.previousCouponPrice,
      total: this.total,
      previousDiscount: this.previousDiscount,
    });

    this.runningAction = newCoupon;

    const result = await newCoupon.execute(cache);

    this.runningAction = null;

    // recalculate total if there is no 'remove' rule;
    if (!this.config.remove && !this.config.stack) {
      if (result.total > this.total) {
        this.total = result.total;
        this.appliedCoupons.resetTotal(result.total);
      }
    }


    const calculatedResult = calculateCouponDiscount(
      // add default result to handle errors from server
      {
        code: this.coupons[this.processIndex].code,
        discount: 0,
        total: this.total,
        wasValidated: false,
        ...(result || {}),
      },
      this.total,
      {
        stack: this.config.stack,
        previousCouponPrice: this.previousCouponPrice || this.total,
        previousDiscount: this.previousDiscount,
      },
    );

    log('log', 'raw result:', result);
    log('log', 'calculated result:', calculatedResult);

    this.appliedCoupons.push(calculatedResult);
    // TODO: find a way to pass htmlCache to the next iteration.
    const {
      /* eslint-disable */
      discount, total, htmlCache,
      /* eslint-enable */
    } = result || {};
    if (discount) {
      this.previousDiscount = discount;
    }
    if (total) {
      this.previousCouponPrice = total;
    }
    await pause(0);
  }

  /**
   * @private
   *
   * Apply coupons
   *
   * @return {Promise<void>}
   */
  async run(cachedState) {
    if (this.status === Status.STARTING) {
      this.status = Status.PROCESS;
    }

    if (this.status !== Status.PROCESS) {
      return;
    }

    // restore previous action
    if (this.status === Status.PROCESS && cachedState) {
      await this.couponStep(cachedState);
      // avoid processIndex falling out of bounds
      if (this.processIndex < this.coupons.length - 1) {
        this.processIndex += 1;
      } else {
        return;
      }
    }
    /* eslint-disable */
    if (this.config.parallel) {
      // TODO: some of the elements might be 'false'. Need to fix it
      const couponsResults = await Promise.all(this.coupons.map((coupon, index) => {

        return this.applyCoupon(index)

      }));
      this.appliedCoupons.pushStack(couponsResults);
    } else {
      for (let processIndex = this.processIndex; processIndex < this.coupons.length; processIndex++) {
        if (this.processIndex !== processIndex) {
          this.processIndex = processIndex;
        }
        if (this.status === Status.STOP) {
          break;
        }
        await this.isPaused;
        await this.couponStep();
        await this.isPaused;
      }
    }
    /* eslint-enable */
  }

  /**
   * @public
   * @async
   *
   * Start CAA execution
   *
   * @param state {*?} - cached state
   * @return {Promise<unknown> | Promise<void>}
   */
  process(state) {
    if (this.isRunning) {
      throw new Error('CAA is already in process');
    }

    const start = async (cachedState) => {
      this.stopObserving();
      const postProcess = await this.preRun();
      await this.isPaused;
      await this.run(cachedState);
      await this.isPaused;
      if (this.status === Status.PROCESS) {
        if (this.config.stack) {
          await this.applyStackResults();
        } else {
          await this.applyBestResult();
        }
      }
      postProcess();
    };

    const process = start(state);
    this.isRunning = process;
    return process.finally(() => {
      this.isRunning = false;
    });
  }

  /**
   * @public
   * @async
   *
   * stops CAA execution. Resolves after finishing running action.
   *
   * @return {Promise<boolean>}
   */
  async stop() {
    this.status = Status.STOP;
    if (this.isPaused) {
      await this.unpause();
    }
    if (this.runningAction) {
      try {
        await this.runningAction.stop();
      } catch (e) {
        // ignore
      }
    }
    await this.isRunning;
    return true;
  }

  /**
   * Pause CAA
   *
   * @param force {boolean} - Should pause running action or pause after
   * @return {Promise<CouponApplier.unpause>}
   */
  async pause(force = false) {
    if (this.isPaused) {
      throw new Error('Caa is already paused');
    }
    // TODO: save pause to state.
    this.isPaused = new Promise((res) => {
      this.unpauseAction = res;
    });
    if (force && this.runningAction) {
      await this.runningAction.pause(this.isPaused);
    }
    return this.unpause;
  }

  /**
   *
   * @return {Promise<void>}
   */
  async unpause() {
    if (this.unpauseAction) {
      this.unpauseAction();
    }
    await this.isPaused;
    this.isPaused = false;
  }

  /**
   * @private
   *
   * calculate best discount and apply coupon
   *
   * @return {Promise<boolean|Status|string>}
   */
  async applyBestResult() {
    if (this.status === Status.STOP) {
      return false;
    }
    const appliedCoupons = this.appliedCoupons.getState();
    const { status, bestIndex, savings } = calculateBestResult(appliedCoupons, this.total);
    if (status === Status.SUCCESS) {
      this.bestCoupon = appliedCoupons[bestIndex];
      this.result = {
        bestCoupon: appliedCoupons[bestIndex],
        savings,
      };
      this.processIndex = bestIndex;
      this.status = Status.FINISHING;
      await this.isPaused;
      await this.successProcess();
      await this.isPaused;
    } else {
      this.status = status;
      // TODO: make it configurable. Add new state
      await this.initialState.restoreInitialCoupons(this.getSharedConfig());
    }
    return status;
  }

  /**
   * @private
   *
   * Calculate stacking discount and apply all successful coupons
   *
   * @return {Promise<Status>}
   */
  async applyStackResults() {
    const {
      successCoupons,
      status,
    } = getSuccessCoupons(this.appliedCoupons.getState(), this.total);
    this.status = Status.FINISHING;
    this.stackCoupons = successCoupons;
    this.stackIndex = 0;
    await this.isPaused;
    if (status === Status.SUCCESS) {
      await this.applyStackCoupons(successCoupons);
    }
    this.status = status;
    await this.isPaused;
    // TODO: make it configurable
    if (
      this.status === Status.FAIL
      && this.initialState
      && this.initialState.coupons
      && this.initialState.coupons.length
      && this.initialState.available
      && this.initialState.total < this.total
    ) {
      // TODO: add new state here
      await this.initialState.restoreInitialCoupons(this.getSharedConfig());
    }
    return status;
  }
}

export { Status } from './enums';
