export const log = (type, ...x) => {
  const debug = sessionStorage.debug || false;
  if (debug) {
    console[type](...x);
  }
};

export const pause = (ms) => {
  return new Promise((resolve) => setTimeout(resolve, ms));
};

export const setSessionStorage = (field, value, id) => {
  const key = `CAA-${id}`;
  if (!sessionStorage.getItem(key)) {
    sessionStorage.setItem(key, JSON.stringify({}));
  }
  const CAA = JSON.parse(sessionStorage.getItem(key));
  CAA[field] = value;
  sessionStorage.setItem(key, JSON.stringify(CAA));
};

/**
 *
 * @param str
 * @param config
 * @return {*|void|string}
 */
export const transformByRx = (str, config = {}) => {
  const {
    from,
    to,
    match,
    matchIndex = 0,
    flags = 'i'
  } = config;
  if (!str) return str;
  let result = `${str}`;
  if (match) {
    try {
      const rx = new RegExp(match, flags);
      const res = result.match(rx);
      if (res && res[matchIndex]) {
        result = res[matchIndex];
      }
    } catch (e) {
      log('log', 'error in transformByRx', config, e);
    }
  }
  if (from && to) {
    try {
      const rx = new RegExp(from, 'gi');
      result = result.replace(rx, to);
    } catch (e) {
      log('log', 'error in transformByRx', config, e);
    }
  }
  return result;
};

export const getSSItem = (name) => JSON.parse(sessionStorage.getItem(name));

export const getSessionStorage = (field, id) => {
  if (!sessionStorage.getItem(`CAA-${id}`)) {
    return null;
  }
  return JSON.parse(sessionStorage.getItem(`CAA-${id}`))[field];
};

export const isHidden = (element) => {
  if (!element) {
    return true;
  }
  const style = global.getComputedStyle(element);
  return style.display === 'none'
      || element.getAttribute('type') === 'hidden'
      ||(element.clientHeight === 0 && element.clientWidth === 0)
      || style.opacity === 0
      || style.visibility === 'hidden'
      || (element.parentElement ? isHidden(element.parentElement) : false);
};

export const hasText = (element, text) => {
  if (!element) {
    return true;
  }
  const trimmedText = text.replace(/['"]/g, '');
  return element.innerText.includes(trimmedText) || element.innerHTML.includes(trimmedText)
};

/**
 *
 * @param elementSelector
 * @param context
 * @return {HTMLHtmlElement[]}
 */
export const queryAll = (elementSelector, context = document) => {
  const selector = getSelector(elementSelector);
  if (!selector) return [];

  // jQuery pseudo classes handle
  const mustBeDropped = (pseudoClass) => ['first', 'visible', 'last', 'contains'].includes(pseudoClass);

  const jQuerySelectors = {};
  const cssSelector = selector.replace(/:([-a-z]+)(?:\((.*)\))?/, (match, key, value) => {
    if (mustBeDropped(key)) {
      jQuerySelectors[key] = value === undefined ? true : value;
      return '';
    }
    return match;
  });

  const matches = Array.from(context.querySelectorAll(cssSelector));

  if (jQuerySelectors.visible) {
    return matches.filter((_) => !isHidden(_));
  }

  if (jQuerySelectors.first) {
    return matches.slice(0, 1);
  }

  if (jQuerySelectors.last) {
    return matches.slice(matches.length - 1, matches.length);
  }

  if (jQuerySelectors.contains) {
    return matches.filter((_) => hasText(_, jQuerySelectors.contains));
  }
  return matches;
};

export const query = (selector, coupon = '', context = document) => {
  if (!selector) {
    return null;
  }
  const selectorWithPromo = selector.replace(/%promo/, (coupon && coupon.code) || '');
  const matches = queryAll(selectorWithPromo, context);
  return matches && matches.length > 0 ? matches[0] : null;
};

/**
 * Remove keys from session storage
 *
 * @param keys {[String]|String} - List of keys to remove from session storage
 */
export const clearSessionStorageKeys = (keys = []) => {
  for (const key of [].concat(keys)) {
    sessionStorage.removeItem(key);
  }
};

export const dispatchEvent = ({ type, key }, target) => {
  let event = null;
  const eventInit = {
    bubbles: true,
    cancelable: true,
    view: window,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
    metaKey: false,
  };
  const mouseTypes = [
    'click',
    'dblclick',
    'mousedown',
    'mousemove',
    'mouseout',
    'mouseover',
    'mouseup',
    'scroll',
  ];
  const keyboardTypes = ['keydown', 'keypress', 'keyup'];
  const htmlTypes = ['change', 'focus', 'blur', 'select', 'submit', 'input'];
  if (mouseTypes.includes(type)) {
    const mouseEventInit = {
      detail: 1,
      screenX: 0,
      screenY: 0,
      clientX: 0,
      clientY: 0,
      button: 0,
      relatedTarget: null,
    };
    event = new MouseEvent(type, Object.assign(mouseEventInit, eventInit));
  } else if (keyboardTypes.includes(type)) {
    const keyboardEventInit = {
      key: key,
      charCode: 0
    };
    event = new KeyboardEvent(type, Object.assign(keyboardEventInit, eventInit));
  } else if (htmlTypes.includes(type)) {
    event = document.createEvent('HTMLEvents');
    event.initEvent(type, true, true);
  }
  target.dispatchEvent(event);
};

export function getCookie(cookieName) {
  const matches = document.cookie.match(
    new RegExp(`(?:^|; )${cookieName.replace(/([.$?*|{}()\[\]\\\/+^])/g, '\\$1')}=([^;]*)`),
  );
  return matches ? matches[1] : undefined;
}

export const parsePrice = (price, preprocessor) => {
  if (!price) return;

  if (price.toString().includes(',') && !price.toString().includes('.')) {
    price = price.toString().replace(',', '.');
  }
  let str = `${price}`;

  if (preprocessor) {
    str = transformByRx(str, preprocessor);
  }

  str = str
    .replace(/^\$|^\€/gim, '')
    .replace('€', '')
    .replace('$', '')
    .replace(/[^\d.,\n]/gim, '')
    .replace(/\n/gim, '')
    .trim();

  const commaIndex = str.indexOf(',');
  const dotIndex = str.indexOf('.');
  if (commaIndex > dotIndex) {
    str = str.replace('.', '')
      .replace(',', '.');
  } else if (commaIndex < dotIndex || dotIndex < 0) {
    str = str.replace(',', '');
  }
  return parseFloat(str);
};

/**
 * Populate element with provided data.
 *
 * @param entry { String | Object } - String to populate. In case of object will be populated recursively with the same data
 * @param code {String=} - replacement for '%promo'
 * @param additionalData {[Object<string, string>]=} - Array of objects. Keys will be replaced values from the object in the first argument
 *
 * @return {string|{}}
 */
export const substitute = (entry, code, additionalData) => {
  const getPin = (str) => {
    const separator = [' & ', ', PIN: '].find((s) => str.indexOf(s) > 0);
    if (separator) {
      return str.split(separator)[0];
    }
    return str;
  };
  const keyValidator = /^%\w+$/;
  if (entry && typeof entry === 'string') {
    let url = `${entry}`;
    if (Array.isArray(additionalData) && additionalData.length) {
      for (let { key, value } of additionalData) {
        if (keyValidator.test(key)) {
          const rx = new RegExp(key, 'g');
          url = url.replace(rx, value);
        } else {
          for (let lastReplaceIndex = -1; url.includes(key) && url.lastIndexOf(key) > lastReplaceIndex;) {
            lastReplaceIndex = url.indexOf(key);
            url = url.replace(key, value);
          }
        }
      }
    }
    return url
        .replace(/%promo/g, code || '')
        .replace(/%pin/g, getPin(code || ''))
        .replace(/%origin/g, document.location.origin)
        .replace(/%pathname/g, document.location.pathname)
        .replace(/%timestamp/g, Date.now().toString());
  }
  if (entry && typeof entry === 'object') {
    const obj = JSON.parse(JSON.stringify(entry));
    return Object.keys(obj)
        .reduce((result, key) => {
          result[key] = substitute(obj[key], code, additionalData);
          return result;
        }, {});
  }
  return entry;
};

export const waitNodes = ({waitFor, waitUntil}) => {
  const waitForArray = Array.isArray(waitFor) ? waitFor : [waitFor];
  const waitUntilArray = Array.isArray(waitUntil) ? waitUntil : [waitUntil];
  const requiredNodes = waitForArray.map(el => query(el));
  const forbiddenNodes = waitUntilArray.map(el => query(el));
  let isObservedChanged = false;
  if (waitFor) {
    let isAcceptable = false;
    for (let el of requiredNodes) {
      if (!isHidden(el)) {
        isAcceptable = true;
        break;
      }
    }
    isObservedChanged = isAcceptable;
  }
  if (waitUntil && !isObservedChanged) {
    let isAcceptable = false;
    for (let el of forbiddenNodes) {
      if (!isHidden(el)) {
        isAcceptable = false;
        break;
      } else {
        isAcceptable = true;
      }
    }
    isObservedChanged = isAcceptable;
  }
  return isObservedChanged
};

export const awaitObserver = ({ waitFor, waitUntil, total, totalAmount }, timeout) => {
  const target = document.body;
  return new Promise((resolve, reject) => {
    if (waitNodes({waitFor, waitUntil})) {
      resolve(true);
    } else {
      const observer = new MutationObserver(() => {
        let isTotalChanged = false;
        // const observedNode = document.querySelector(waitFor || waitUntil);
        const currentTotal = getAmount(total);
        isTotalChanged = currentTotal && currentTotal !== totalAmount;

        const isObservedChanged = waitNodes({waitFor, waitUntil});

        if (isObservedChanged || isTotalChanged) {
          observer.disconnect();
          resolve(true);
        }
      });
      observer.observe(target, {
        attributes: true,
        childList: true,
        subtree: true
      });
      const timer = setTimeout(() => {
        observer.disconnect();
        resolve(true);
      }, (timeout || timeout === 0) ? timeout : 5000);
    }
  });
};

/**
 * Replace alert, confirm, prompt with console.log to avoid page blocking
 *
 * @return {function(...[*]=)} - function to restore blockers.
 */
export const muteBlockers = () => {
  const savedPageBlockers = new Map();
  const blockers = [
    'alert',
    'confirm',
    'prompt',
  ];
  for (const blocker of blockers) {
    savedPageBlockers.set(blocker, window[blocker]);
    Object.defineProperty(window, blocker, {
      value: console.log,
      writable: true,
      configurable: true,
    });
  }
  return () => {
    for (let blocker of savedPageBlockers.keys()) {
      window[blocker] = savedPageBlockers.get(blocker);
    }
  };
};

/**
 * make HTML document from string
 *
 * @param htmlText {string} - string to make document from
 *
 * @return {Document}
 */
export const parseHtml = (htmlText = '') => {
  return new DOMParser().parseFromString(htmlText, 'text/html');
};

// TODO: rewrite it to make it more readable
export const getAmount = (rule = {}, _data = document.body) => {
  log('log', 'getAmount()', rule);
  let data = _data;

  if (!rule) {
    log('error', {
      result: RESULTS.Error,
      message: 'rule in not found',
      functionName: 'getAmount()',
    });
    return false;
  }

  if (typeof data === 'string') {
    data = parseHtml(data);
  }

  if (typeof rule === 'string') {
    const node = query(rule, '', data);
    log('log', 'node', node, node && node.innerText);
    return node && parsePrice(node.innerText);
  }

  if (typeof rule === 'object') {
    const {from, to, match} = rule;
    const parser = ((from && to) || match) && rule ;

    // TODO: consider removing it.
    if (rule.rx) {
      const rx = new RegExp(rule.rx, 'g');
      const curData = String(data)
          .trim();
      const matches = [];
      let res = rx.exec(curData);
      while (res !== null) {
        matches.push(res[1]);
        res = rx.exec(curData);
      }
      if (matches.length) {
        return parsePrice(matches.length === 1 ? matches[0] : matches, parser);
      }
    }
    if (rule.path || rule.field) {
      let path = rule.path || rule.field;
      let arr = path.split('.');
      let obj = data;
      while (arr.length > 0) {
        obj = obj[arr.shift()];
      }
      return parsePrice(obj, parser);
    }
    if (rule.selector) {
      const node = query(rule.selector, '', data);
      return node ? parsePrice(node.innerText, parser) : 0;
    } else {
      if (parser && typeof _data === 'string') {
        return parsePrice(_data, parser);
      }
    }
  }
  return 0;
};

/**
 * Get url from element (or just return provided string)
 *
 * @param url {String|{selector: string, attribute: string?}}
 * @param doc { HTMLDocument? } - document to get data from.
 *
 * @return {string|undefined|boolean}
 */
export const getUrl = (url, doc = document) => {
  if (typeof url === 'string') {
    return url;
  } else {
    if (typeof url === 'object') {
      return retrieveDataFromPage(url, doc);
    }
  }
};

/**
 * Detect if action is request
 *
 * @param type {string} request type
 *
 * @return {boolean}
 */
export const isRequestAction = (type) => [
  'get',
  'head',
  'post',
  'delete',
  'options',
  'put',
  'patch',
  'trace',
].includes(type);

export const getUpdatedPage = async () => {
  const req = await fetch(location.href);
  const htmlString = await req.text();
  return parseHtml(htmlString);
};

/**
 *
 * @param remove
 * @param apply
 * @return {boolean}
 */
export const requireCache = ({remove, apply}) => {
  if (!remove || !apply) return false;
  const removeRules = [].concat(remove);
  const applyRules = [].concat(apply);
  const validRule = removeRules.find(({url}) => typeof url === 'object');
  const lastActionType = applyRules[applyRules.length - 1].type.toLowerCase();
  const isRequest = isRequestAction(lastActionType) || lastActionType === 'submit';
  return validRule && isRequest;
};

/**
 * Get value from json object
 *
 * @param src {Object} - Target json object
 * @param path {String} - Path to the target property
 * @param splitter {String=} - Separator for searching key in response
 *
 * @return {*} - target value from src object
 */
export const getJSONValue = (src, path, splitter = '//') => path.split(splitter)
  .reduce((target, currentKey) => target && target[currentKey], src);

export async function DOMContentLoaded() {
  if (/(comp|inter|loaded)/.test(document.readyState)) {
    return Promise.resolve();
  }
  return new Promise((resolve) => document.addEventListener('DOMContentLoaded', resolve));
}

export const findConfig = configs => {
  const currentUrl = window.location.href;
  return (configs || []).find(({ url }) => {
    const urls = Array.isArray(url) ? url : [url];
    let isMatch = false;
    try {
      isMatch = !!urls.find((u) => {
        const rx = new RegExp(u, 'ig');
        return currentUrl.indexOf(u) >= 0 || rx.test(currentUrl);
      });
    } catch (e) {
      isMatch = false;
    }
    return isMatch;
  });
};

