export const waitFields = (config = {}) => new Promise((res, rej) => {
  const { controls, requiredFields = [] } = config;

  if (!controls) {
    rej();
  } else {
    const interval = setInterval(()  => {
      if (checkFields({ controls, requiredFields })) {
        clearInterval(interval);
        res();
      }
    }, 300);
  }
});

export const validateCoupon = (couponCode, responseCode, data) => {
  if (data.length) {
    return !!data.find((code) => code[responseCode] === couponCode);
  }
  return data[responseCode] === couponCode;
};

/**
 * @param data {[{
 *     params: [{
 *         key: string,
 *         path: string?,
 *     }],
 *     name: string,
 *     type: string?,
 * }]}
 *
 * @return {[{
 *     key: string,
 *     value: (string|number|boolean),
 * }]}
 */
export const getLocalStorageData = (data = []) => {
  const substituteData = [];

  for (const item of data) {
    if (!item || !item.params || !item.params.length) continue;

    let storageItem = localStorage.getItem(item.name);

    if (!storageItem) continue;

    if (item.type === 'json') {
      storageItem = JSON.parse(storageItem);
    }

    for (const { path, key } of item.params) {
      let dataItem = null;

      if (path) {
        const value = getJSONValue(storageItem, path);
        if (value) {
          dataItem = {
            key,
            value
          };
        }
      } else {
        dataItem = {
          key,
          value: storageItem
        };
      }

      dataItem && substituteData.push(dataItem);
    }
  }

  return substituteData;
};

export const getAdditionalData = config => {
  log('log', 'getAdditionalData', config);
  if (!config) return {};

  let cookieValue = null;
  let localData = null;

  if (config.cookie) {
    cookieValue = getCookie(config.cookie.key);
  }

  if (config.localStorageData) {
    localData = getLocalStorageData(config.localStorageData);
  }


  return {
    localData,
    cookieValue
  };
};

export const execCustomScript = (
  code,
  responseType,
  { endTimeout, intervalTimeout } = {},
) => {
  const script = document.createElement('script');

  const key = `pageData_${Math.random()
    .toFixed(2) * 1000}`;

  script.textContent = code.replace(/%key/gim, key);
  document.head.appendChild(script);

  return new Promise((resolve) => {
    let timeout;
    const interval = setInterval(() => {
      if (sessionStorage[key]) {
        const storageValue = responseType === 'json'
          ? JSON.stringify(sessionStorage[key] || {})
          : sessionStorage[key];
        if (timeout) clearTimeout(timeout);
        clearInterval(interval);
        delete sessionStorage[key];
        document.head.removeChild(script);
        resolve(storageValue);
      }
    }, intervalTimeout || 500);

    timeout = setTimeout(() => {
      if (interval) clearInterval(interval);
      delete sessionStorage[key];
      document.head.removeChild(script);
      resolve(false);
    }, endTimeout || 4000);
  });
};

export const getPageVariable = param => {
  const code = `sessionStorage.%key = JSON.stringify(window.${param});`;
  return execCustomScript(code, 'json');
};

/**
 * @public
 *
 * get data from dom or from global variable
 *
 * @param configData {[{
 *     key: string,
 *     selector?: string,
 *     attribute?: string,
 *     variable?: string,
 * }] | undefined}
 * @param doc {Document?} - cached document
 *
 * @return {Promise<{}>}
 */
export const getCustomPageData = async (configData, doc = document) => {
  if (!configData || !configData.length) return {};

  const data = {};

  for (const item of configData) {
    if (item.selector) {
      try {
        const value = retrieveDataFromPage(item, doc);
        if (value) {
          data[item.key] = value;
        }
      } catch (err) {

      }
    }

    if (item.variable) {
      // TODO: consider removing this shit. executing custom code from a server is super uncool
      const value = await getPageVariable(item.variable);
      if (value) {
        try {
          data[item.key] = JSON.parse(value);
          if (typeof data[item.key] === 'string') {
            try {
              data[item.key] = JSON.parse(data[item.key]);
            } catch (e) {
              log('log', 'error', e);
            }
          }
        } catch (e) {
          log('log', 'catch parse variable', e);
          data[item.key] = value;
        }
      }
    }
  }

  return data;
};

export const mergeData = (data, additionalData, ...params) => {
  if (!data) return '';

  if (typeof data === 'string') {
    return data;
  }

  return { ...additionalData, ...data };
};

export const parseStringResponse = async ({ coupon, response, previousCouponPrice, xhr, action, total }) => {
  let responseData;
  const currentApplyAction = action;
  const curCoupon = {
    code: coupon.code,
    total: previousCouponPrice,
  };

  if (!xhr) {
    responseData = await response.text();
  } else {
    responseData = Promise.resolve(response);
  }

  if (currentApplyAction.response.discount) {
    curCoupon.discount = getAmount(currentApplyAction.response.discount,
      responseData
    );
    curCoupon.total = total - curCoupon.discount;
  } else {
    curCoupon.total = getAmount(
      currentApplyAction.response.total,
      responseData,
    );
    curCoupon.discount = parseFloat((total - curCoupon.total).toFixed(2));
  }

  curCoupon.wasValidated = false;
  // TODO: move it to process to make code more readable
  // try to parse response as html to validate coupon
  if (action.couponValidation) {
    const validator = action.couponValidation;
    if (validator) {
      try {
        const doc = parseHtml(responseData);
        const isValid = checkCouponSuccess(validator, curCoupon.code, doc);
        curCoupon.wasValidated = isValid;
        curCoupon.invalidated = !isValid;
      } catch (e) {
        curCoupon.invalidated = true;
      }
    }
  }

  return curCoupon;
};

export const parseJSONResponse = async ({ coupon, response, action, total, previousCouponPrice }) => {
  let responseData = await response.clone().json();
  const currentApplyAction = action;
  const curCoupon = { ...coupon };

  let totalValue = total;
  let curDiscount = null;
  let couponValidation = null;

  if (currentApplyAction.response.findProperty) {
    const {
      path,
      fieldValue,
      field,
      errorValidation,
    } = currentApplyAction.response.findProperty;

    if (responseData[errorValidation]) {
      responseData = { amount: total };
    } else {
      responseData = getJSONValue(responseData, path).find(
        (resp) => {
          const value = getJSONValue(resp, field);
          return value === fieldValue || value === substitute(fieldValue, coupon.code);
        }
      );
    }
  }

  if (currentApplyAction.response.path) {
    if (currentApplyAction.response.selector) {
      const htmlData = getJSONValue(
        responseData,
        currentApplyAction.response.path
      );
      if (htmlData && currentApplyAction.response.selector) {
        const data = parseHtml(htmlData);
        totalValue = getAmount(currentApplyAction.response.selector, data) || total
        // const data = document.createElement('div');
        // data.innerHTML = htmlData;
        // totalValue =
        //   parsePrice(
        //     data.querySelector(currentApplyAction.response.selector).innerText,
        //   ) || total;
      }
    } else {
      totalValue = parsePrice(
        getJSONValue(responseData, currentApplyAction.response.path),
        currentApplyAction.response
      ) || total;
    }

  } else {
    totalValue = total;
  }

  if (currentApplyAction.response.total) {
    totalValue = parsePrice(
      getJSONValue(responseData, currentApplyAction.response.total) || total,
        currentApplyAction.response
    );
  }
  if (totalValue && totalValue < total) {
    curDiscount = total - totalValue;
  }

  if (currentApplyAction.response.discount) {
    if (typeof currentApplyAction.response.discount === 'string') {
      curDiscount = getJSONValue(
        responseData,
        currentApplyAction.response.discount,
      );
    } else {
      const { type } = currentApplyAction.response.discount;
      // TODO: consider removing it because there is no other type
      if (!type || (type && type === 'number')) {
        curDiscount = getJSONValue(
          responseData,
          currentApplyAction.response.discount.path,
        );
      }
      if (curDiscount) {
        curDiscount = parsePrice(curDiscount, currentApplyAction.response.discount);
      }
    }
  }

  if (!curDiscount) {
    // TODO: handle it
    curDiscount = total - totalValue;

    if (
      previousCouponPrice
      && Number(previousCouponPrice) === totalValue
    ) {
      curDiscount = 0;
    }
  }

  if (currentApplyAction.couponValidation) {
    couponValidation = getJSONValue(
      responseData,
      currentApplyAction.couponValidation.path,
    );
    // TODO: check it
    const isValidCoupon = validateCoupon(
      curCoupon.code,
      currentApplyAction.couponValidation.field,
      couponValidation,
    );
    if (!isValidCoupon) {
      curCoupon.total = total;
      curCoupon.discount = 0;
      curCoupon.wasValidated = false;
      curCoupon.invalidated = true;
      currentApplyAction.success = false;
      log('log', `coupon ${curCoupon.code} hasn't passed validation`);
    } else {
      curCoupon.wasValidated = true;
      curCoupon.invalidated = false;
    }
  } else {
    curCoupon.wasValidated = false;
  }

  curCoupon.total = totalValue;
  curCoupon.discount = curDiscount;
  return curCoupon;
};

// TODO: consider merging it with getHtmlData
export const parseHTMLResponse = async ({ coupon, response, xhr, action, previousCouponPrice, config, total, cacheResponse }) => {
  let responseData;
  const currentApplyAction = action;
  const curCoupon = { ...coupon };
  if (!xhr) {
    responseData = await response.text();
  } else {
    responseData = Promise.resolve(response);
  }

  if (currentApplyAction.response.discount) {
    curCoupon.discount = getAmount(currentApplyAction.response.discount, responseData);
    curCoupon.total = total - curCoupon.discount;
  } else {
    curCoupon.total = getAmount(currentApplyAction.response.total, responseData);

    if (
      previousCouponPrice
      && Number(previousCouponPrice) === curCoupon.total
      && (!config.remove || config.stack)
    ) {
      curCoupon.discount = 0;
    } else {
      curCoupon.discount = parseFloat((total - curCoupon.total).toFixed(2));
    }
  }

  const htmlCache = parseHtml(responseData);
  if (cacheResponse) {
    curCoupon.htmlCache = htmlCache;
  }

  const validator = action.couponValidation;
  if (validator) {
    try {
      const isValid = checkCouponSuccess(validator, curCoupon.code, htmlCache);
      curCoupon.wasValidated = isValid;
      curCoupon.invalidated = !isValid;
    } catch (e) {
      curCoupon.invalidated = true;
      log('log', 'not validated');
    }
  }

  return curCoupon;
};

export const getPageData = ({ doc = document, controls, code }) => {

  if (!controls) return null;

  const [total, discount] = [controls.total, controls.discount]
      .map(target => getAmount(target, doc) || 0);

  return {total, discount, code};
};

export const handleData = (data = {}, submitResult) => {
  const result = {};
  const doc = submitResult ? parseHtml(submitResult) : document;
  Object.keys(data).forEach((key) => {
    const value = data[key];
    if (value && typeof value === 'object') {
      try {
        result[key] = retrieveDataFromPage(value, doc);
      } catch (e) {
        log('log', 'error handleData', e);
      }
    } else {
      result[key] = value;
    }
  });
  return result;
};

export const getHtmlData = (htmlText, response = {}, config, code, previousCouponPrice) => {
  const doc = parseHtml(htmlText);
  const { controls } = config;
  const total = getAmount(controls.total, doc);
  const discount = getAmount(response.discount, doc) || getAmount(controls.discount, doc);
  return {
    total: total || previousCouponPrice,
    discount: discount || 0,
    code,
  };
};

/**
 *
 * Get elements from target node
 *
 * @param selector {string} - Selector string
 * @param target {HTMLDocument|Document|HTMLHtmlElement|HTMLElement} - node, that should contain the element
 *
 * @return {HTMLHtmlElement[]}
 */
export const queryAllFromNode = (selector, target = document.documentElement) => {
  if (!target || !selector) throw new Error('no selector or no target in getNestedElementFromNode');
  if (target instanceof HTMLDocument || target instanceof HTMLHtmlElement) {
    // the target is top element on the page or Document itself
    const result = queryAll(selector, target);
    if (!result || !result.length) {
      throw new Error('couldn\'t find element by selector');
    }
    return result;
  }
  if (!(target instanceof HTMLElement)) {
    throw new Error('target isn\'t html element in getNestedElementFromNode');
  }
  const topElement = getTopElement(target);
  const allElements = queryAll(selector, topElement);
  const result = allElements.filter(el => target.contains(el));
  if (!result || !result.length) {
    throw new Error('couldn\'t find element by selector');
  }
  return result;
};

/**
 *
 * Get element from target node
 *
 * @param selector {string} - Selector string
 * @param target {HTMLDocument|Document|HTMLHtmlElement|HTMLElement} - node, that should contain the element
 *
 * @return {HTMLHtmlElement}
 */
export const  queryFromNode = (selector, target = document.documentElement) => queryAllFromNode(selector, target)[0];

/**
 *
 * @param selector
 * @param target
 * @return {HTMLHtmlElement}
 */
export const queryFromNodeOrDoc = (selector, target) => {
  try {
    return queryFromNode(selector, target);
  } catch (e) {
    const topElement = getTopElement(target);
    return queryFromNode(selector, topElement);
  }
};

/**
 * Get data from html document
 *
 * @param data {string|{attribute: string?, selector: string}}
 * @param doc {Document?}
 * @return {string|boolean}
 */
export const retrieveDataFromPage = (data, doc = document) => {
  if (!data)
    throw new Error('no config');

  if (typeof data === 'string') {
    return retrieveDataFromPage({selector: data}, doc);
  } else {
    const {selector, attribute} = data;
    let el;
    try {
      el = queryFromNodeOrDoc(selector, doc);
    } catch (e) {
      el = query(selector);
    }
    if (!el)
      throw new Error(`can't get element. selector: ${selector}`);
    if (!attribute) {
      switch (el.tagName) {
        case ('A'):
          return el.getAttribute('href');
        case ('INPUT'):
          return el.getAttribute('value');
        default:
          return el.innerText || el.content || el.value;
      }
    } else {
      return attribute === 'innerText' ? el.innerText : el.getAttribute(attribute);
    }
  }
};

/**
 * Detect discount/price for a coupon
 *
 * @param coupon {{code: string}}
 * @param response {Response} - response for the request
 * @param action {Action}
 * @param xhr {boolean} - flag to control the method for response reading
 * @param previousCouponPrice {number} - price before current coupon was applied
 * @param total {number} - total price before applying any coupon
 * @param config {Object} - Config object
 * @param cacheResponse {boolean?} - flag to parse response and return it in the result object.
 *
 * @return {Promise<boolean|{}>}
 */
export const parseTotal = async ({ coupon, response, action, xhr, previousCouponPrice, total, config, cacheResponse }) => {

  let parsedCoupon = {...coupon};

  if (
    !response
    || !response.status
    || response.status >= 300
    || !action.response
    || !coupon
  ) {
    log('log', 'error', 'Response status is not ok', response.status, response);
    return {
      ...parsedCoupon,
      total,
      discount: 0,
      invalidated: true,
    };
  }

  const type = action.response && action.response.type;

  if (type) {
    switch (type) {
      case 'string':
        parsedCoupon = await parseStringResponse({
          coupon,
          response,
          xhr,
          action,
          previousCouponPrice,
          total,
        });
        log('log', 'parsedCoupon string', parsedCoupon);
        break;
      case 'json':
        parsedCoupon = await parseJSONResponse({
          coupon,
          response,
          action,
          previousCouponPrice,
          total,
        });
        log('log', 'parsedCoupon json', parsedCoupon);
        break;
      case 'html':
        parsedCoupon = await parseHTMLResponse({
          coupon,
          response,
          xhr,
          action,
          previousCouponPrice,
          config,
          total,
          cacheResponse,
        });
        log('log', 'parsedCoupon html', parsedCoupon);
        break;
      default:
        parsedCoupon.total = getAmount(config.controls.total);

        if (
          previousCouponPrice
          && Number(previousCouponPrice) === parsedCoupon.total
          && (!config.remove || !config.remove.length)
        ) {
          parsedCoupon.discount = 0;
        } else {
          parsedCoupon.discount = parseFloat(
            (total - parsedCoupon.total).toFixed(2),
          );
        }

        if (
          config.controls.errorMessage
          && (Number(coupon.discount) || 0) === 0
        ) {
          const errorMessage = query(config.controls.errorMessage);
          log('log', 'errorMessage', errorMessage);
          if (errorMessage) {
            coupon.errorMessage = errorMessage.innerText;
          }
        }
        break;
    }
  }

  return parsedCoupon;
};

/**
 * Defines best coupon to apply
 *
 * @param {Array} appliedCoupons Array of appliance results.
 * @param {Number} initialTotal Cached total amount.
 *
 * @return {Object} Best result object.
 */
export const calculateBestResult = (appliedCoupons, initialTotal) => {
  let bestDiscount = 0;
  // let bestTotal = initialTotal;
  let bestIndex = -1;

  // Iterate over each result in order to define best coupon
  appliedCoupons.forEach(({ /*total, */discount, success }, index) => {
    if (discount > bestDiscount) {
      bestDiscount = discount;
      bestIndex = index;
    }
    if (!bestDiscount && success && bestIndex < 0) {
      bestIndex = index;
    }
  });

  if (/* bestTotal < initialTotal || bestDiscount > 0 */bestIndex > -1) {
    return {
      status: Status.SUCCESS,
      bestIndex,
      savings: /*bestTotal < initialTotal ? initialTotal - bestTotal :*/ parseFloat(bestDiscount),
    }
  } else {
    return {
      status: Status.FAIL,
    }
  }
};

/**
 * @param requiredData [{
 *     key: string,
 *     selector: string?,
 *     attribute: string?,
 *     variable: string?,
 * }]
 * @param doc
 *
 * @return {Promise<[{key: string, value: string}]>}
 */
export const getContentData = async (requiredData = [], doc = document) => {
  const result = [];

  for (const el of requiredData) {
    const {key, selector, attribute, variable, cookie} = el;
    if (selector) {
      try {
        const value = retrieveDataFromPage({attribute, selector});
        result.push({ key, value: transformByRx(value, el) });
      } catch (e) {

      }
    } else {
      if (variable) {
        const rawResult = await getPageVariable(variable);
        if (rawResult) {
          let value;
          try {
            value = JSON.parse(rawResult);
            if (typeof value === 'string') {
              try {
                value = JSON.parse(value);
              } catch (e) {
                log('log', 'error', e);
              }
            }
          } catch (e) {
            log('log', 'catch parse variable', e);
            value = rawResult;
          }
          result.push({key, value: transformByRx(value, el)});
        }
      }
      if (cookie) {
        const cookieValue = transformByRx(getCookie(cookie), el);
        if (cookieValue) {
          result.push({
            key,
            value: cookieValue,
          })
        }
      }
    }
  }
  return result;
};

/**
 * Iterate over coupons to calculate stacked discount and filter out invalid coupons
 *
 * @param coupons {[{code: string, discount: number?, total: number?}]} - Array of appliance results
 * @param initialTotal {number} - total price without coupons
 *
 * @return {{successCoupons: [{code: string, discount: number?, total: number?}], savings: number, status: Status}}
 */
export const getSuccessCoupons = (coupons, initialTotal) => {
  let savings = 0;
  const successCoupons = [];

  for (const {discount, total, code, success} of coupons) {
    let couponDiscount = discount;
    if (!couponDiscount && total && total < initialTotal) {
      couponDiscount = initialTotal - total;
    }
    if (couponDiscount || success) {
      savings += couponDiscount;
      successCoupons.push({discount, total, code, success});
    }
  }

  return {
    savings,
    successCoupons: savings > 0 ? successCoupons.filter(({discount}) => discount) : successCoupons,
    status: successCoupons.length ? Status.SUCCESS : Status.FAIL,
  }
};

/**
 *
 * @param result {{
 *     discount: Number,
 *     total: Number,
 *     code: String,
 *     wasValidated: boolean,
 * }}
 * @param initialTotal {Number} - total before caa started
 * @param stack {boolean}
 * @param previousCouponPrice {number}
 * @param previousDiscount {number}
 * }}
 *
 * @return {AppliedCoupon}
 */
// TODO: consider discount calculation while parsing response data
export const calculateCouponDiscount = (result, initialTotal, {stack, previousCouponPrice, previousDiscount = 0}) => {
  const { discount, total, code, wasValidated, invalidated } = result;
  if (!stack) {
    const totalDifference = initialTotal > total ? initialTotal - total : 0;
    const bestDiscount = Math.max(totalDifference, discount);
    const calculatedDiscount = bestDiscount > 0 ? bestDiscount : 0;
    return {
      code,
      discount: invalidated ? 0 : calculatedDiscount,
      success: !invalidated && !!(calculatedDiscount || wasValidated),
      wasValidated,
    };
  } else {
    const totalDifference = (total < previousCouponPrice) ? previousCouponPrice - total : 0;
    const discountDifference = (previousDiscount < discount) ? discount - previousDiscount : 0;
    const calculatedDiscount = Math.max(totalDifference, discountDifference, 0);
    return {
      code,
      discount: invalidated ? 0 : calculatedDiscount,
      success: !invalidated && !!(calculatedDiscount || wasValidated),
      wasValidated,
    }

  }
};

export const getCouponFromPage = (config, doc = document) => {
  if (!config) {
    throw new Error('no config');
  }

  return transformByRx(retrieveDataFromPage(config, doc), config);
};

/**
 * validate coupon appliance
 *
 * @param couponConfig {object?} - coupon validation config
 * @param code {string} - coupon code
 * @param doc {Document?}
 *
 * @return {boolean}
 */
export const checkCouponSuccess = (couponConfig, code, doc = document) => {
  if (!couponConfig) throw new Error('no config in checkCouponSuccess')

  const couponEl = getCouponFromPage(couponConfig, doc);
  const isExtractable = couponConfig.extractable || couponConfig.from;
  return !isExtractable ? !!couponEl : couponEl.toLowerCase() === code.toLowerCase();
};

/**
 * Set promo code into the input
 *
 * @param code {string} - Coupon code
 * @param selector {string} - Input selector
 *
 * @return {Promise<unknown>}
 */
export const typeCode = (code, selector) => {
  const promoInput = query(selector);
  if (!selector || !promoInput) {
    throw new Error(`no element found for selector: ${selector}`);
  }
  dispatchEvent({ type: 'focus' }, promoInput);
  promoInput.value = code;
  ['change', 'input', 'keyup'].forEach((_type) => dispatchEvent({ type: _type }, promoInput));
  return pause(0);
};


/**
 *
 * @param elem {HTMLHtmlElement|HTMLElement|HTMLDocument|Document|Node|ParentNode}
 * @return {HTMLHtmlElement|HTMLDocument|Document|Node|ParentNode|*}
 */
const getTopElement = (elem) => elem&& elem.parentNode ? getTopElement(elem.parentNode) : elem;


/**
 *
 * @param func
 * @param wait
 * @param opt
 * @return {Function}
 */
export const customThrottle = (func, wait, opt = {}) => {
  const {leading} = opt;
  let lastInvoked = leading ? 0 : Date.now();
  let timeout;
  let lastArgs;

  const throttled = (...args) => {
    if (lastInvoked + wait > Date.now()) {
      if (timeout) clearTimeout(timeout);
      lastArgs = args;
      timeout = setTimeout(() => {
        throttled(...args)
      }, (lastInvoked + wait) - Date.now());
    } else {
      lastInvoked = Date.now();
      func(...args);
    }
  };

  throttled.force = () => {
    lastInvoked = Date.now();
    if (timeout) clearTimeout(timeout);
    func(...lastArgs);
  };

  return throttled;
};
