import React, { useEffect, useState } from 'react';
import UnsecureNotification from '@betternet/shared/content/components/safe-shopping/unsecure-notifications';
import { sendContentMessage } from '@softomate/rework-party';
import { useLogger, useSessionStorage } from 'react-use';
import { sendContentReport } from '@common-shared/Reporting/reporting-methods';
import UnsecureWebsiteSVG from '../../../static/assets/elements/safe-shopping/unsecure-website.svg';

function UnsecureWebsite() {
  const [show, setShow] = useState(undefined);
  const [count, setCount] = useSessionStorage('betternent_unsecure_website', 0);
  useLogger('UnsecureWebsite', show, count);
  useEffect(() => {
    if (show === undefined && count < 1) {
      setShow(true);
    } else if (show === undefined) {
      setShow(false);
    }
  }, [count, show]);
  useEffect(() => {
    if (show) {
      sendContentReport('unsecure_website_notification', 'shown', window.location.hostname);
    }
  }, [show]);

  const onClose = () => {
    console.log('on close UnsecureWebsite');
    setCount(count + 1);
    setShow(false);
    sendContentReport('unsecure_website_notification', 'close', window.location.hostname);
  };

  const onClick = () => {
    setShow(false);
    sendContentMessage({ method: 'antiPhishing.closeCurrentTab' });
    sendContentReport('unsecure_website_notification', 'leave', window.location.hostname);
  };

  if (!show) return null;

  return (
    <UnsecureNotification
      image={() => <UnsecureWebsiteSVG />}
      domain={window.location.hostname}
      title='Unsecure website'
      info='We found this site in our database of unsecure websites. This website could steal your personal data or harm your computer. Betternet recommends leaving this site.'
      buttonText='Leave website'
      onClose={onClose}
      onClick={onClick}
    />
  );
}

export default UnsecureWebsite;
