export default class DistributionTool {
  /**
   * Distribution tool args
   * @name DTArgs
   * @param {RegExp} homeRx Project Home page regexp
   * @param {string} name Meta tag name for the distribution tool
   * @param {string} content Meta tag content for the distribution tool
   * @return {object} args
   */

  /**
   * Injects Meta tag on the customer's website for distribution tool usage
   * @param {DTArgs} args Meta tag name for distribution tool on the customer's website
   */
  constructor({ homeRx, name, content }) {
    this.homeRx = homeRx;
    this.name = name;
    this.content = content;
    this.init();
  }

  init() {
    if (this.homeRx.test(window.location.href) && !document.head.querySelector(`[name=${this.name}]`)) {
      const meta = document.createElement('meta');
      meta.name = this.name;
      if (this.content) {
        meta.content = this.content;
      }
      document.head.appendChild(meta);
    }
  }
}
