import {
  getCustomPageData,
  getUrl,
  mergeData,
  query,
  substitute,
  log,
} from './utils';
import { requestTypes, ActionTypes } from './enums';
/* eslint-disable */
/**
 * Rule for DefaultAction
 *
 * @typedef {Object} Rule
 *
 * @property {boolean} [xhr] - flag to use XMLHttpRequest instead of fetch (preserve FormData body)
 * @property {string} submit - selector for click target
 * @property {string} [url]
 * @property {string} [method] - request method
 * @property {object} [body] - request body
 * @property {object} [headers] - request headers
 * @property {ResponseConfig} [response] - response config
 * @property {number} [postTimeout] - timeout after request action
 * @property {number} [timeout] - timeout after click or other non-response action
 * @property {[string]|string} [waitFor] - selector(s) to await appearance after action is performed. Disregards timeout if ONE of element was found on the page.
 * @property {[string]|string} [waitUntil] - selector(s) to await hiding after action is performed. Disregards timeout if ALL of element was found on the page.
 * @property {{discount: string|{selector: string, from: string, to:string}}} [controls] - total/discount selectors for page
 * @property {string|{selector: string, from: string, to: string}} [couponConfig] - config to validate coupon on page
 * @property {string} actionMethod - type of required action
 */
/* eslint-enable */

const {
  CLICK,
  REQUEST,
} = ActionTypes;

/**
 * Process config from json
 */
export default class Rule {
  /**
   *
   * @param ruleConfig
   * @param controls
   * @param code
   * @param pageData
   * @param forceClick
   * @param doc
   *
   * @return {Rule}
   */
  constructor(ruleConfig, { controls }, code, pageData, forceClick, doc = document) {
    /** @private */
    this.rawRule = ruleConfig;
    /** @private */
    this.doc = doc;
    /** @private */
    this.code = code;
    /** @private */
    this.pageData = pageData;
    /** @private */
    this.controls = controls;
    /** @private */
    this.isReady = false;
    /** @private */
    this.forceClick = forceClick;
    // this.couponConfig = ruleConfig.couponValidation || controls.coupon
  }

  /**
   * Parse config and prepare data
   *
   * @return {Promise<void>}
   */
  async setFromConfig() {
    if (this.isReady) {
      return;
    }
    const rule = this.rawRule;
    this.setType(rule);
    if (!this.isClick) {
      this.setMethod(rule);
      const {
        contentType,
        requiredBodyData,
        requiredHeadersData,
        data,
        headers,
        form,
      } = rule;
      this.setUrl(rule);
      const formData = this.formData(rule);
      const defaultHeaders = {};
      if (contentType) {
        defaultHeaders['Content-Type'] = contentType;
      }
      const submitForm = (form && form.submitForm) || this.isSubmit;
      const [customBody, customHeaders] = [requiredBodyData, requiredHeadersData]
        .map(await getCustomPageData);
      const mergedBodyData = !submitForm ? mergeData(
        this.substitute(data || formData),
        customBody,
      ) : formData;
      const mergedHeadersData = this.substitute(
        mergeData(headers || defaultHeaders, customHeaders),
      );
      const shouldEncodeBody = rule.type.toLowerCase() !== 'submit' && form && !form.submitForm;
      this.body = shouldEncodeBody ? Object.keys(mergedBodyData)
        .map(
          (key) => `${encodeURIComponent(key)}=${encodeURIComponent(mergedBodyData[key])}`,
        )
        .join('&') : mergedBodyData;
      this.headers = mergedHeadersData;
      this.xhr = rule.xhr || (rule.form && rule.form.submitForm);
      this.setResponse(rule);
    }
    if (rule.submit) {
      this.submit = this.substitute(rule.submit);
    }
    this.setCouponValidation();
    const copiedRule = { ...rule };
    // remove useless props
    [
      'contentType',
      'requiredBodyData',
      'requiredHeadersData',
      'data',
      'form',
    ].forEach((prop) => {
      delete copiedRule[prop];
    });

    // add remaining rules to result object
    Object.keys(copiedRule).forEach((key) => {
      if (!this[key]) {
        this[key] = copiedRule[key];
      }
    });
    this.isReady = true;
  }

  /**
   * Set action type
   *
   * @param type
   * @param submit {string?} - selector to click
   */
  setType({ type, submit }) {
    this.type = (this.forceClick && submit) ? CLICK : type.toLowerCase();
    /** @type {ActionTypes} */
    this.actionMethod = requestTypes.includes(this.type) ? REQUEST : this.type;
  }

  /**
   * set coupon validation rule
   */
  setCouponValidation() {
    if (this.isClick) this.couponValidation = this.rawRule.couponValidation || this.controls.coupon;
    else this.couponValidation = this.rawRule.couponValidation;
  }

  /**
   * set type for fetch request
   *
   * @param form
   */
  setMethod({ form }) {
    if (this.isSubmit) {
      const formElement = this.doc.querySelector(form);
      this.method = formElement.getAttribute('method') || 'POST';
    } else {
      this.method = this.type;
    }
  }

  /**
   * set url for request
   * @param url
   * @param form
   */
  setUrl({ url, form }) {
    if (!this.isClick) {
      if (!url && form && typeof form === 'string') {
        const formEl = this.doc.querySelector(form);
        try {
          this.url = formEl.url || formEl.getAttribute('action');
        } catch (err) {
          log('error', 'can\'t get url from form');
        }
      } else {
        this.url = getUrl(this.substitute(url));
      }
    }
  }

  /**
   * @return {boolean}
   */
  get isSubmit() {
    return this.type === 'submit';
  }

  get isRequest() {
    return requestTypes.includes(this.type);
  }

  get isClick() {
    return this.type === 'click';
  }

  substitute(data) {
    return substitute(data, this.code, this.pageData);
  }

  /**
   * Parse data from page to use as body/query in request
   *
   * @param rule
   * @return {{}|FormData}
   */
  formData(rule) {
    if (rule.form) {
      const selector = this.isSubmit ? rule.form : rule.form.selector;
      const form = query(selector);
      const formDataEl = form ? new FormData(form) : null;
      if (formDataEl) {
        const result = {};
        const submitForm = this.isSubmit || rule.form.submitForm;
        const data = this.isSubmit ? rule.data : rule.form.data;
        const { getAllData, formData } = rule.form;
        if (getAllData) {
          /* eslint-disable */
          for (const key of formDataEl.keys()) {
            const formDataValue = formDataEl.get(key);
            result[key] = formDataValue || '';
          }
          /* eslint-enable */
        }
        if (formData) {
          formData.forEach((key) => {
            const formDataValue = formData.get(key);

            if (formDataValue) {
              result[key] = formDataValue;
            }
          });
        }

        if (data && !submitForm) {
          data.forEach(({ key, value }) => {
            if (key && value) {
              result[key] = this.substitute(value);
            }
          });
        }
        if (submitForm) {
          if (data) {
            let dataArr;
            if (Array.isArray(data)) {
              dataArr = data;
            } else {
              const dataKeys = Object.keys(data);
              if (dataKeys.includes('key') && dataKeys.includes('value')) {
                dataArr = [].concat(data);
              } else {
                dataArr = [];
                dataKeys.forEach((key) => {
                  dataArr.push({ key, value: data[key] });
                });
              }
            }
            dataArr.forEach(({ key, value }) => {
              if (key && value) {
                formDataEl.set(key, this.substitute(value));
              }
            });
          }
          return formDataEl;
        }
        return result;
      }
    }

    return {};
  }

  setResponse({ response }) {
    if (!response) {
      if (this.isSubmit) {
        this.response = { type: 'html' };
        const { discount, total } = this.controls;
        if (discount) {
          this.response.discount = this.substitute(discount);
        }
        if (total) {
          this.response.total = total;
        }
      }
    } else if (response.type === 'html') {
      const inheritedConfig = {
        total: this.controls.total,
        discount: this.controls.discount,
      };
      this.response = this.substitute({
        ...inheritedConfig,
        ...response,
      });
    } else {
      this.response = this.substitute(response);
    }
  }
}
