import React, { useEffect, useState } from 'react';
import BaseWrapper from '@betternet/shared/content/components/base-wrapper/base-wrapper';
import Button from '@betternet/shared/ui/components/button';
import './caa-notification.less';
import classNames from 'classnames';
import { sendContentInternalAnalytics, sendContentReport } from '@common-shared/Reporting/reporting-methods';
import DefaultMerchantIcon from '../../../static/assets/elements/coupons/default-merchant-icon.svg';

function Notification({ apply, close, count, domain }) {
  const [active, setActive] = useState(false);
  useEffect(() => {
    setActive(true);
    sendContentReport('auto_applier', 'shown', domain);
    sendContentInternalAnalytics('ui_view', { view_name: 'ntf_discount' });
  }, []);
  const onApply = () => {
    apply();
    sendContentInternalAnalytics('ui_click', { view_name: 'ntf_discount', action_name: 'btn_apply_deals' });
    sendContentReport('auto_applier', 'click', domain);
  };
  const onClose = () => {
    close();
    sendContentInternalAnalytics('ui_click', { view_name: 'ntf_discount', action_name: 'btn_close' });
    sendContentReport('auto_applier', 'close', domain);
  };
  return (
    <div
      className={classNames('caa-notification', {
        'caa-notification--init': !active,
        'caa-notification--active': active
      })}
    >
      <BaseWrapper className='caa-notification__wrapper'>
        <DefaultMerchantIcon />
        <h2 className='caa-notification__text'>{count} coupons available to get the best price</h2>
        <h3 className='caa-notification__domain'>{domain}</h3>
        <p className='caa-notification__tooltip'>
          Click apply coupons and we’ll test and apply the coupons we found for you
        </p>
        <Button blue fullWidth onClick={onApply}>
          Apply deals
        </Button>
        <Button transparent fullWidth onClick={onClose}>
          Not now
        </Button>
      </BaseWrapper>
    </div>
  );
}

export default Notification;
