import {
  awaitObserver,
  checkCouponSuccess,
  dispatchEvent,
  getAmount,
  getPageData,
  log,
  pause,
  query,
} from '../utils';
import DefaultAction from './DefaultAction';
import { ActionStep } from '../enums';

const {
  RUN, POSTRUN, RESULT,
} = ActionStep;

export default class Click extends DefaultAction {
  async [RUN]() {
    const { submit } = this.action;
    const element = query(submit);
    if (!element) {
      throw new Error(`click event: No element - ${submit}`);
    }
    ['mouseover', 'mousedown', 'click', 'mouseup', 'focus']
      .forEach((type) => dispatchEvent({ type }, element));
    return pause(0);
  }

  async [POSTRUN]() {
    const { controls } = this.action;
    const totalAmount = getAmount(controls.total) || this.total;
    const { waitFor, waitUntil, timeout } = this.action;
    await awaitObserver(
      {
        waitFor,
        waitUntil,
        total: controls.total,
        totalAmount,
      },
      timeout,
    );
  }

  [RESULT]() {
    const result = getPageData({
      code: this.code,
      controls: this.action.controls,
    });

    const validator = this.action.couponValidation;
    if (validator) {
      try {
        const isValid = checkCouponSuccess(validator, this.code);
        result.wasValidated = isValid;
        result.invalidated = !isValid;
      } catch (e) {
        log('error', 'result invalidated ', e);
        result.invalidated = true;
      }
    }

    return result;
  }
}
