import { sendContentMessage } from '@softomate/rework-party';
import { queryCache, useMutation, useQuery } from 'react-query';
import { useEffect, useState } from 'react';

const getIntroShowStatus = () => sendContentMessage({ method: 'coupons.isIntroAccepted' });
const onIntroAccept = () => sendContentMessage({ method: 'coupons.onIntroAccept' });

export function useAcceptSafeShoppingFeature() {
  const { data: isShoppingFeatureAccepted } = useQuery('is-intro-accepted', getIntroShowStatus);
  const [onShoppingFeatureAccept] = useMutation(onIntroAccept, {
    onSuccess: () => queryCache.invalidateQueries('is-intro-accepted')
  });
  const [isShoppingFeatureClosed, onShoppingFeatureClose] = useState(false);
  return {
    isShoppingFeatureAccepted,
    onShoppingFeatureAccept,
    isShoppingFeatureClosed,
    onShoppingFeatureClose
  };
}

const getCouponTutorialCompleted = () => sendContentMessage({ method: 'coupons.isTutorialCompleted' });
const onCouponTutorialComplete = () => sendContentMessage({ method: 'coupons.onTutorialComplete' });

export function useCouponTutorial() {
  const [isCouponsTutorialClosed, onCouponsTutorialClose] = useState(true);
  const { data: isCouponsTutorialCompleted } = useQuery('is-tutorial-completed', getCouponTutorialCompleted);
  const [onCouponsTutorialComplete] = useMutation(onCouponTutorialComplete, {
    onSuccess: () => {
      onCouponsTutorialClose(true);
      return queryCache.invalidateQueries('is-tutorial-completed');
    }
  });
  useEffect(() => {
    if (isCouponsTutorialCompleted && !isCouponsTutorialClosed) {
      onCouponsTutorialClose(true);
    }
  }, [isCouponsTutorialCompleted]);
  return {
    isCouponsTutorialCompleted,
    onCouponsTutorialComplete,
    isCouponsTutorialClosed,
    onCouponsTutorialClose
  };
}
