import React, { useState } from 'react';
import BaseWrapper from '@betternet/shared/content/components/base-wrapper/base-wrapper';
import Button from '@betternet/shared/ui/components/button';
import './caa-progress.less';
import { sendContentReport } from '@common-shared/Reporting/reporting-methods';
import { floor } from 'lodash';

function Progress({
  isSuccess = false,
  isFail = false,
  isProgress = true,
  willSave = undefined,
  count = 0,
  index = 0,
  close
}) {
  const [startDate] = useState(() => Date.now());
  let progress;

  const status = Math.min(((1 + index) / count) * 100, 100);
  const onClose = () => {
    close();
    sendContentReport('auto_applier', 'close', Math.floor((Date.now() - startDate) / 1000));
  };

  if (isProgress) {
    progress = (
      <>
        <img
          src={browser.extension.getURL('static/assets/elements/caa/magic-hat.svg')}
          alt='Betternet'
          className='caa-progress__logo'
        />
        {willSave && <h2 className='caa-progress__text'>You will save {willSave}</h2>}
        <div className='caa-progress__progress-bar'>
          <div className='caa-progress__progress-filter' style={{ width: `${status}%` }} />
        </div>
        <p className='caa-progress__subtext'>Testing coupons...</p>
      </>
    );
  } else if (isFail) {
    progress = (
      <>
        <img
          src={browser.extension.getURL('static/assets/elements/caa/magic-hat.svg')}
          alt='Betternet'
          className='caa-progress__logo'
        />
        <h2 className='caa-progress__text'>You already have the best price</h2>
        <Button className='caa-progress__button' blue fullWidth onClick={close}>
          Proceed to checkout
        </Button>
      </>
    );
  } else if (isSuccess) {
    const [decimal, fraction = ''] = `${floor(willSave, 2)}`.split('.');
    progress = (
      <>
        <img
          src={browser.extension.getURL('static/assets/elements/caa/magic-hat.svg')}
          alt='Betternet'
          className='caa-progress__logo'
        />
        {willSave && (
          <h2 className='caa-progress__text'>
            {/* eslint-disable-next-line jsx-a11y/accessible-emoji */}
            You will save <span className='caa-progress__decimal'>💰</span>
            <span className='caa-progress__decimal'>
              {decimal}
              {fraction && '.'}
            </span>
            {fraction && <span className='caa-progress__fraction'>{fraction}</span>}
          </h2>
        )}
        {!willSave && <h2 className='caa-progress__text'>You already have the best price</h2>}
        <Button className='caa-progress__button' blue fullWidth onClick={close}>
          Thank you
        </Button>
      </>
    );
  }

  return (
    <>
      <div className='caa-progress'>
        <BaseWrapper close={onClose}>{progress}</BaseWrapper>
      </div>
      <div className='caa-progress__backdrop' />
    </>
  );
}

export default Progress;
