import merge from 'lodash.merge';
import { getStorageData, setStorageData } from './utils';

export default class DynamicFetcher {
  constructor(args) {
    const {
      name, url, options, updateDelay, errorDelay,
    } = args;
    if (!name || !url) {
      throw new Error('name or url parameter is undefined');
    }
    this.name = name;
    this.url = url;
    this.options = options || {};
    this.updateDelay = updateDelay || 4 * 60 * 60 * 1000;
    this.errorDelay = errorDelay || this.updateDelay / 10;

    this.data = null;
  }

  parse(data) {
    return data;
  }

  fetch(url, options) {
    const params = options ? merge(this.options, options) : this.options;
    return fetch(url || this.url, params).then((response) => response.json());
  }

  update(force) {
    clearInterval(this.updateInterval);
    return getStorageData([
      `${this.name}.lastUpdate`,
      `${this.name}.data`,
    ]).then((items) => {
      const lastUpdate = items[`${this.name}.lastUpdate`];
      const cashedData = items[`${this.name}.data`];
      const now = new Date().getTime();

      if (lastUpdate + this.updateDelay >= now && cashedData && !force) {
        this.data = cashedData;
        const nextUpdate = lastUpdate + this.updateDelay - now;
        this.updateInterval = setInterval(() => this.update(), nextUpdate);
        return this.data;
      }
      return this.fetch()
        .then((data) => this.parse(data))
        .then((data) => {
          if (data) {
            this.data = data;
            const cash = {};
            cash[`${this.name}.data`] = data;
            cash[`${this.name}.lastUpdate`] = now;

            setStorageData(cash);
            this.updateInterval = setInterval(() => this.update(), this.updateDelay);
          } else {
            const errorUpdate = this.data ? this.errorDelay : this.errorDelay / 10;
            this.updateInterval = setInterval(() => this.update(true), errorUpdate);
          }
          return this.data;
        })
        .catch((e) => {
          console.log(e);
          const errorUpdate = this.data ? this.errorDelay : this.errorDelay / 10;
          this.updateInterval = setInterval(() => this.update(true), errorUpdate);
        });
    });
  }
}
