import React from 'react';
import './base-wrapper.less';
import classNames from 'classnames';
import BetternetLogo from '../../../static/assets/logo/betternet-logo.svg';
import CloseCross from '../../../static/assets/elements/close-small.svg';

function BaseWrapper({ children, close, className }) {
  return (
    <div className='base_wrapper'>
      <header className='base_wrapper__header'>
        <BetternetLogo />
        {close && <CloseCross onClick={close} />}
      </header>
      <section className={classNames('base_wrapper__content', className)}>{children}</section>
    </div>
  );
}

export default BaseWrapper;
