import RegisteredActions from '../Actions';

/**
 * Actions fabric
 */
export default class ActionsFabric {
  /**
   * @param type {string} - type of action to execute
   * @param props {{
   *   rule: Rule,
   *   code: string,
   *   previousTotal: number,
   *   cacheResponse: boolean?,
   *   total: number?,
   *   config: object?,
   *   cache: Document?,
   * }} - Data for passing to action
   */
  constructor(type, props) {
    return new RegisteredActions[type](props);
  }
}
