import './content.less';
import '@common-shared/Utils/CrossBrowserVaribles';
import ReactDOM from 'react-dom';
import React, { useEffect, useState } from 'react';
import { useQuery } from 'react-query';
import CAAController from '@betternet/shared/content/components/caa/caa-controller';
import { sendContentMessage } from '@softomate/rework-party';
import UnsecureWebsite from '@betternet/shared/content/components/safe-shopping/unsecure-website';
import ContentCouponsController from '@betternet/shared/content/components/coupons/content-coupons-controller';

const initContent = () => sendContentMessage({ method: 'coupons.merchants.initContentMessageHandler' });
const getCaaConfigs = () => sendContentMessage({ method: 'coupons.caa.configs.data' });
const isPhishingUrl = url => sendContentMessage({ method: 'antiPhishing.isPhishingUrl', arguments: [url] });
const isPhishingSite = () => isPhishingUrl(window.location.href);

function App() {
  const { data } = useQuery('merchant', initContent);
  const { data: isPhishing } = useQuery('isPhishingSite', isPhishingSite);
  const [configs, setConfigs] = useState(null);
  const [coupons, setCoupons] = useState(null);
  const [caaNoConfig, setCaaNoConfig] = useState(false);

  useEffect(() => {
    if (!configs?.length && (data?.merchant || coupons?.length)) {
      getCaaConfigs().then(configs => {
        setConfigs(configs);
      });
    }
  }, [data, coupons]);

  useEffect(() => {
    try {
      const coupons = JSON.parse(localStorage.betternet_coupons);
      if (coupons?.length > 0) {
        setCoupons(coupons);
      }
    } catch (e) {}
  }, []);

  useEffect(() => {
    global.setMerchantCoupons = coupons => {
      setCoupons(coupons);
      localStorage.betternet_coupons = JSON.stringify(coupons);
    };
  }, []);

  useEffect(() => {
    global.setCAAConfigs = setConfigs;
  }, []);

  if (isPhishing) {
    return <UnsecureWebsite />;
  }

  const merchant = data?.merchant;
  const offers = merchant?.offers;
  let caaCoupons = offers?.filter(({ promoCode }) => promoCode)?.map(({ promoCode }) => ({ code: promoCode }));
  caaCoupons = caaCoupons ?? coupons;
  const showContentCoupons = caaNoConfig === true && offers?.filter(({ type }) => type === 'COUPON')?.length > 0;
  return (
    <div className='betternet_content'>
      {configs && caaCoupons && <CAAController configs={configs} coupons={caaCoupons} onNoConfig={setCaaNoConfig} />}
      {showContentCoupons && <ContentCouponsController offers={offers} />}
    </div>
  );
}

class Content {
  constructor() {
    const shadowroot = Content.createShadowRoot();
    const domContainer = shadowroot.querySelector('#betternet_content_root');
    ReactDOM.render(React.createElement(App), domContainer);
  }

  static createShadowRoot() {
    const wrapper = document.createElement('div');
    wrapper.className = 'betternet-wrapper';
    document.body.appendChild(wrapper);
    const shadowroot = wrapper.attachShadow({ mode: 'open' });
    shadowroot.innerHTML = `
      <style>
        .betternet_content_clean * {}
      </style>
      <style>@import url("${browser.extension.getURL('content/styles.css')}");</style>
      <div id="betternet_content_root" class="betternet_content_clean"></div>
    `;
    return shadowroot;
  }
}

global.content = new Content();
