import StepIterator from '../StepIterator';
import { log, requireCache, typeCode } from '../utils';
import { CouponStepNames, Status, Step } from '../enums';
import RulesGenerator from '../RulesGenerator';
import Action from './ActionsFabric';

const { APPLY_COUPON, REMOVE_COUPON } = CouponStepNames;

export default class StepBase extends StepIterator {
  constructor({
    code = '', config, previousCouponPrice, status, total,
  }) {
    super();
    this.code = code;
    this.config = config;
    this.status = status;
    this.previousCouponPrice = previousCouponPrice;
    this.total = total;
    this.stepType = null;
    this.setSteps([APPLY_COUPON, REMOVE_COUPON]);
    this.stepResult = APPLY_COUPON;
    this.state = {};
  }

  /**
   * @public
   *
   * get current coupon execution state
   *
   * @return {{
   *  name: string?,
   *  stepState: {
   *    name: string?,
   *  }?,
   *  result: any?,
   * }}
   */
  getState() {
    const state = super.getState();
    state.stepState = this.runningAction.getState();
    state.result = this.result;
    return state;
  }

  /**
   * @override
   * @param stepName {string}
   */
  setState(stepName) {
    this.state.name = stepName;
  }

  /**
   * @override
   */
  clearState() {
    this.state = {};
  }

  /**
   * @public
   *
   * @param cache {{
   *  name: string?,
   *  result: object?,
   *  stepState: *?,
   *  }?}
   * @return {Promise<*>}
   */
  async execute(cache) {
    const { name, result, stepState } = cache || {};

    const performStep = async (step, arg) => {
      try {
        const actionResult = await step(arg);
        if (step.stepName === this.stepResult) {
          this.result = actionResult;
        }
      } catch (e) {
        log('error', 'error executing action in CouponStep', e);
      }
    };

    let wasPerformed = !!name;
    /* eslint-disable */
    for (const step of this.getValidSteps()) {
      if (this.stopped) break;
      if (wasPerformed && (name === step.stepName)) {
        if (stepState) {
          await performStep(step, stepState);
        }
        wasPerformed = false;
        continue;
      }
      if (!wasPerformed) {
        await performStep(step);
      }
    }
    /* eslint-enable */
    this.clearState();
    return this.result || result;
  }

  /**
   * @public
   *
   * Execute action
   *
   * @param rule {Rule}
   * @param cacheResponse {boolean?}
   * @param actionCache {*?}
   *
   * @return {function(): Promise}
   */
  action(rule, cacheResponse, actionCache) {
    const actionData = {
      code: this.code,
      previousTotal: this.previousCouponPrice,
      config: this.config,
      // applying: this.stepType === Step.APPLYING,
      total: this.total,
      cacheResponse,
      rule,
    };
    const action = new Action(rule.actionMethod, actionData);
    this.runningAction = action;
    return action.execute(actionCache);
  }

  /**
   * @private
   *
   * Detect if next action need to save response from server
   *
   * @return {boolean}
   */
  preserveResponse() {
    // TODO: it is better to detect it in caa itself
    return this.status === Status.STARTING
      || (requireCache(this.config) && this.stepType === Step.APPLYING);
  }

  async [APPLY_COUPON](actionCache) {
    /** @type {string} */
    const promoSelector = this.config.controls.promo;
    try {
      await typeCode(this.code, promoSelector);
    } catch (e) {
      log('error', 'can\'t type code', e);
    }

    const { apply } = this.config;

    if (!apply) {
      throw new Error('no apply config');
    }

    const rules = new RulesGenerator({
      config: this.config,
      rules: apply,
      code: this.code,
      // TODO: it is better to detect it in caa itself
      forceClick: this.status === Status.FINISHING,
    });

    let result;
    /* eslint-disable */
    // TODO: implement rule caching
    for await (const rule /** @type {Rule}*/ of rules) {

      try {
        result = await this.action(rule, requireCache(this.config), actionCache);
      } catch (e) {
        log('log', 'error applying coupon', e);
      }
    }
    /* eslint-enable */

    return result;
  }

  async [REMOVE_COUPON](actionCache) {
    const { remove } = this.config;

    if (!remove) {
      throw new Error('no remove config');
    }
    if (this.status === Status.FINISHING) {
      throw new Error('Applying success coupon. Pass through');
    }
    const rules = new RulesGenerator({
      config: this.config,
      rules: remove,
      code: this.code,
      // TODO: it is better to detect it in caa itself
      forceClick: this.status === Status.STARTING,
    });

    // TODO: implement cache response somehow
    let cache;
    /* eslint-disable */
    for await (const rule of rules) {
      try {
        const removeResult = await this.action(rule, this.preserveResponse(), actionCache);

        if (this.preserveResponse()) {
          cache = removeResult && removeResult.htmlCache;
        }
      } catch (e) {
        log('log', 'error removing coupon');
      } finally {
        // this.removeCoupon.getState = null;
      }
    }
    /* eslint-enable */
    return cache;
  }

  async stop() {
    try {
      if (this.runningAction) {
        await this.runningAction.stop();
      }
    } catch (e) {
      log('error', 'can\'t stop step execution ', e);
    }
    return super.stop();
  }
}
