import StepIterator from '../StepIterator';
import { log } from '../utils';
import { ActionStep } from '../enums';

const {
  PRERUN, RUN, POSTRUN, RESULT,
} = ActionStep;

// TODO: create an interface
export default class Base extends StepIterator {
  constructor() {
    super();
    this.setSteps([PRERUN, RUN, POSTRUN, RESULT]);
  }

  [PRERUN]() {
    log('log', 'executing prerun action');
  }

  [RUN]() {
    log('log', 'executing run action');
  }

  [POSTRUN]() {
    log('log', 'executing postrun action');
  }

  [RESULT]() {
    log('log', 'executing result action');
  }

  /**
   *
   * @param cache
   * @return {Promise<{}>}
   */
  async execute(cache = {}) {
    const { name } = cache;
    let wasPerformed = !!name;
    let lastActionResult = cache;
    /* eslint-disable */
    for (const step of this.getValidSteps()) {
      if (this.stopped) break;
      if (wasPerformed && (name === step.stepName)) {
        wasPerformed = false;
        continue;
      }
      if (!wasPerformed) {
        lastActionResult = await step(lastActionResult);
      }
    }
    /* eslint-enable */
    return lastActionResult;
  }
}
