export default class CashbackBase {
  constructor({ suppressionPatterns, activationPatterns }) {
    this.suppressionPatterns = suppressionPatterns;
    this.activationPatterns = activationPatterns;
    this.suppressions = [];
    this.activations = [];
  }

  checkActivation({ url, tabId }) {
    const patterns = this.activationPatterns;
    const isActivationUrl = patterns.find((p) => url && p && url.match(p));
    if (isActivationUrl) {
      this.setActivation({ url, tabId });
    }
  }

  checkSuppression({ url, tabId }) {
    if (!url) {
      return false;
    }
    const activation = this.activations.find((a) => a.tabId === tabId);
    if (activation) {
      return false;
    }
    const isSuppression = this.suppressionPatterns.find((pattern) => {
      try {
        return url.search(pattern) > -1;
      } catch (e) {
        return url.indexOf(pattern) > 0;
      }
    });
    if (isSuppression) {
      this.setSuppression({ url, tabId });
      return true;
    }
    return false;
  }

  setSuppression({ tabId }) {
    let suppression = this.suppressions.find((s) => s.tabId === tabId);
    if (!suppression) {
      suppression = { tabId };
      this.suppressions.push(suppression);
    }
    clearTimeout(suppression.timer);
    suppression.timer = setTimeout(() => {
      this.suppressions = this.suppressions.filter((s) => s.tabId !== tabId);
    }, 40000);
  }

  setActivation({ tabId, deeplink }) {
    let activation = this.activations.find((a) => a.tabId === tabId);
    if (!activation) {
      activation = { tabId };
      this.activations.push(activation);
    }
    clearTimeout(activation.timer);
    activation.deeplink = activation.deeplink || deeplink;
    activation.timer = setTimeout(() => {
      this.activations = this.activations.filter((a) => a.tabId !== tabId);
    }, 30000);
  }
}
