/**
 * @overview
 * Track existing coupons on the page before CAA starts working
 * Helps to restore initial coupons that was applied by user but wasn't provided by extension
 */

import BaseObserver from './BaseObserver';
import AdvanceObserver from './AdvanceObserver';

export default class PageObserver {
  constructor(config, ...args) {
    const { from, extractable } = config.controls.coupon || {};
    const isExtractable = from || extractable;
    if (isExtractable) {
      return new AdvanceObserver(config, ...args);
    }
    return new BaseObserver(config);
  }
}
