import { sendContentMessage } from '@softomate/rework-party';

export function sendContentReport(category, action, label, value) {
  sendContentMessage({
    method: 'reporting.report',
    arguments: [category, action, label, value]
  });
}

// eslint-disable-next-line camelcase
export function sendContentInternalAnalytics(category, { view_name, action_name, action_detail }) {
  sendContentMessage({
    method: 'reporting.internal',
    arguments: [category, { view_name, action_name, action_detail }]
  });
}
