import EventEmitter from 'events';
import { customThrottle, log, queryAll } from '../utils';

/**
 * @extends EventEmitter
 */
export default class BaseObserver extends EventEmitter {
  /**
   * @param config {{}} - config from json
   */
  constructor(config) {
    super();
    this.rawConfig = config;
    this.config = config.controls.coupon;
    this.available = !!this.config;
    if (this.available) {
      this.coupons = this.getExistingCoupons();
      this.cartNode = document.documentElement;
    }
  }

  /**
   * get coupons html elements
   *
   * @return {{el: HTMLHtmlElement}[]}
   */
  getExistingCoupons() {
    return queryAll(this.config).map((el) => ({ el }));
  }

  /**
   * Emit events if coupons state has changed
   */
  observer() {
    const pageCoupons = this.getExistingCoupons();
    if (!this.coupons.length && pageCoupons.length) {
      this.emit('hasAppliedCoupon');
    }
    if (this.coupons.length && !pageCoupons.length) {
      this.emit('couponsCleared');
    }
    this.coupons = pageCoupons;
  }

  /**
   * Start observing coupons on current page
   */
  startObserving() {
    if (this.available) {
      if (!this.cartObserver) {
        this.cartObserver = new MutationObserver(customThrottle(() => {
          this.observer();
        }, 500));
      }
      log('log', 'start observing cart');
      this.cartObserver.observe(this.cartNode, {
        childList: true,
        subtree: true,
      });
    }
  }

  /**
   * Stop coupons observation
   */
  stopObserving() {
    if (this.available && this.cartObserver) {
      log('log', 'stop cart observing');
      this.cartObserver.disconnect();
    }
  }

  /**
   * change saved total for coupons
   * @param total
   */
  setTotal(total) {
    this.total = total;
  }

  updateCouponInfo() {
    log('warn', 'can\'t update info for not extractable coupon');
  }

  restoreInitialCoupons() {
    log('warn', 'can\'t restore coupon');
  }

  getState() {
    log('log', 'can\'t save coupons');
    return null;
  }
}
