import {
  getContentData,
  getCookie,
  getLocalStorageData,
} from './utils';

import Rule from './Rule';

/**
 * @implements {AsyncIterable}
 */
export default class RulesGenerator {
  constructor({
    config, rules, code, forceClick = false,
  }, doc = document) {
    this.config = config;
    this.rawRules = [].concat(rules);
    this.code = code;
    this.doc = doc;
    this.forceClick = forceClick;
  }

  [Symbol.asyncIterator]() {
    const rulesCopy = [...this.rawRules];
    return {
      /**
       * rule for each step
       * @return {Promise<{done: boolean, value: Rule}|{done: boolean}>}
       */
      next: async () => {
        if (!rulesCopy.length) {
          return { done: true };
        }
        const rule = rulesCopy.shift();
        const pageData = await RulesGenerator.acquireAdditionalData({
          cookie: this.config.cookie || rule.cookie,
          localStorageData: this.config.localStorageData || rule.localStorageData,
          requiredContentData: [
            ...(rule.requiredContentData || []),
            ...(this.config.requiredContentData || []),
          ],
        }, this.doc);

        const formattedRule = new Rule(
          rule,
          this.config,
          this.code,
          pageData,
          this.forceClick,
          this.doc,
        );
        await formattedRule.setFromConfig();
        return {
          done: false,
          value: formattedRule,
        };
      },
    };
  }

  /**
   * @async
   * @public
   *
   * get data from cookie, localStorage, dom
   *
   * @param cookie
   * @param localStorageData
   * @param requiredContentData
   * @param doc
   * @return {Promise<[{key: string, value: *}]>}
   */
  static async acquireAdditionalData({
    cookie,
    localStorageData,
    requiredContentData,
  },
  doc = document) {
    let result = [];
    if (cookie) {
      const cookieValue = getCookie(cookie.key);
      if (cookieValue) {
        result.push({
          key: '%cookieValue',
          value: getCookie(cookie.key),
        });
      }
    }

    if (localStorageData) {
      result = result.concat(getLocalStorageData(localStorageData));
    }

    if (requiredContentData) {
      const contentData = await getContentData(requiredContentData, doc);
      result = result.concat(contentData);
    }

    return result;
  }
}
