import Base from './Base';
import { log, typeCode } from '../utils';
import { ActionStep } from '../enums';

const {
  PRERUN,
} = ActionStep;

export default class DefaultAction extends Base {
  constructor({
    rule, code, previousTotal,
  }) {
    super();
    // TODO: rename 'action' into 'rule'
    this.action = rule;
    this.code = code;
    this.previousTotal = previousTotal;
  }

  async [PRERUN]() {
    try {
      /** @type {string} */
      const promoSelector = this.action.controls.promo;
      await typeCode(this.code, promoSelector);
    } catch (e) {
      log('error', 'can\'t type code', e);
    }
  }

  /**
   *
   * @return {{name: string}}
   */
  getState() {
    return { name: this.state };
  }
}
