import {
  checkCouponSuccess, getHtmlData, log, parseHtml, pause,
} from '../utils';
import { ActionStep } from '../enums';
import DefaultAction from './DefaultAction';

const {
  RUN, POSTRUN, RESULT,
} = ActionStep;

export default class Submit extends DefaultAction {
  constructor(args) {
    super(args);
    const { config, cacheResponse } = args;
    this.cacheResponse = cacheResponse;
    this.config = config;
  }

  async [RUN]() {
    const { action } = this;
    const url = action.url;
    if (!url) {
      return false;
    }
    const response = await fetch(url, {
      method: action.method,
      body: new URLSearchParams(action.body),
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
      },
    });
    return response.text();
  }

  async [POSTRUN](response) {
    if (this.action.postTimeout) {
      await pause(this.action.postTimeout);
    }
    return response;
  }

  async [RESULT](response) {
    const result = await getHtmlData(
      response,
      this.action.response,
      this.config,
      this.code,
      this.previousTotal,
    );

    const validator = this.action.couponValidation;

    try {
      result.wasValidated = validator
        && checkCouponSuccess(validator, this.code, parseHtml(response));
    } catch (e) {
      log('log', 'unchecked results');
    }

    if (this.cacheResponse && result) {
      return {
        ...result,
        htmlCache: parseHtml(response),
      };
    }

    return result;
  }
}
