import {
  log,
} from './utils';
/**
 * @typedef {Iterable} StepIterator
 *
 * @property {[string]} steps - array of methods to iterate over
 *
 * Class that provides iterator for steps.
 * Intended to have step by step iterations, that can be cached and restored later.
 * Note: Single instance can be iterated over ONLY ONCE.
 */
export default class StepIterator {
  constructor() {
    this.getState = this.getState.bind(this);
    this.setSteps([]);
    /** @private */
    this.state = false;
    /**
     * @private
     * @type {Promise|boolean}
     * */
    this.isRunning = false;
    /** @type {Promise|boolean} */
    this.pausePromise = false;
  }

  /**
   *
   * @public
   * Terminate execution
   *
   * @return {Promise<void>}
   */
  async stop() {
    this.stopped = true;
    try {
      await this.isRunning;
    } finally {
      log('log', 'execution terminated');
    }
  }

  /**
   * @param promiseToAwait {Promise|boolean}
   */
  async pause(promiseToAwait = false) {
    this.pausePromise = promiseToAwait;
    if (this.isRunning) {
      try {
        await this.isRunning.pause(promiseToAwait);
      } catch (e) {
        log('error', 'error pausing StepIterator', e);
      }
    }
  }

  setSteps(steps = []) {
    this.steps = new Set(steps);
  }

  /**
   * @public
   *
   * get current step name
   *
   * @return {this.state}
   */
  getState() {
    return this.state;
  }

  /**
   * Set current state of action
   * will be invoked before executing a step
   *
   * @param stepName {string}
   */
  setState(stepName) {
    this.state = stepName;
  }

  clearState() {
    this.state = null;
  }

  /**
   * @return {[function<Promise<*>>]}
   */
  getValidSteps() {
    const result = [];
    for (const stepName of this.steps.values()) {
      if (this[stepName] && typeof this[stepName] === 'function') {
        /**
         * @async
         *
         * @param args
         * @return {Promise<void>}
         */
        const stepWrapper = async (...args) => {
          this.steps.delete(stepName);
          try {
            await this.pausePromise;
          } catch (e) {
            log('error', 'error awaiting pause', e);
          }
          this.setState(stepName);
          this.isRunning = stepWrapper;
          const action = await this[stepName](...args);
          this.clearState();
          this.isRunning = null;
          return action;
        };
        stepWrapper.stepName = stepName;
        result.push(stepWrapper);
      }
    }
    return result;
  }

  /**
   * command to execute iterations.
   * @param continueFrom
   *
   * @return {Promise<void>}
   */
  async execute(continueFrom) {
    let wasPerformed = !!continueFrom;

    /* eslint-disable */
    for (const step of this.getValidSteps()) {
      // TODO: consider throwing an Error
      if (this.stopped) break;
      if (wasPerformed && (continueFrom === step.stepName)) {
        wasPerformed = false;
        continue;
      }
      if (!wasPerformed) {
        await step();
      }
    }
    /* eslint-enable */
    this.clearState();
  }
}
