import React, { useEffect, useState } from 'react';
import { sendContentInternalAnalytics, sendContentReport } from '@common-shared/Reporting/reporting-methods';
import classNames from 'classnames';
import BaseWrapper from '@betternet/shared/content/components/base-wrapper/base-wrapper';
import Button from '@betternet/shared/ui/components/button';
import DefaultMerchantIcon from '../../../static/assets/elements/coupons/default-merchant-icon.svg';
import './content-coupons-notification.less';

function ContentCouponsNotification({ apply, close, count, domain }) {
  const [active, setActive] = useState(false);
  useEffect(() => {
    setActive(true);
    sendContentReport('content-coupons-notification', 'shown', domain);
    sendContentInternalAnalytics('ui_view', { view_name: 'ntf_coupons' });
  }, []);
  const onShow = () => {
    apply();
    sendContentInternalAnalytics('ui_click', { view_name: 'ntf_coupons', action_name: 'btn_show_coupons' });
    sendContentReport('content-coupons-notification', 'click', domain);
  };
  const onClose = () => {
    close();
    sendContentInternalAnalytics('ui_click', { view_name: 'ntf_coupons', action_name: 'btn_close' });
    sendContentReport('content-coupons-notification', 'close', domain);
  };
  return (
    <div
      className={classNames('content-coupons-notification', {
        'content-coupons-notification--init': !active,
        'content-coupons-notification--active': active
      })}
    >
      <BaseWrapper className='content-coupons-notification__wrapper'>
        <DefaultMerchantIcon />
        <h2 className='content-coupons-notification__text'>{count} coupons available to get the best price</h2>
        <h3 className='content-coupons-notification__domain'>{domain}</h3>
        <Button blue fullWidth onClick={onShow}>
          Show coupons&nbsp;
          <svg width='6' height='10' viewBox='0 0 6 10' fill='none' xmlns='http://www.w3.org/2000/svg'>
            <path
              d='M1 1.22656L4.77124 4.9978L1 8.76903'
              stroke='white'
              strokeWidth='2'
              strokeLinecap='round'
              strokeLinejoin='round'
            />
          </svg>
        </Button>
        <Button transparent fullWidth onClick={onClose}>
          Not now
        </Button>
      </BaseWrapper>
    </div>
  );
}

export default ContentCouponsNotification;
