import React, { useCallback, useEffect, useState } from 'react';
import { sendContentInternalAnalytics, sendContentReport } from '@common-shared/Reporting/reporting-methods';
import './content-coupons-notification.less';
import { useAcceptSafeShoppingFeature } from '@betternet/shared/hooks/safe-shoppiing-feature';
import { useSessionStorage } from 'react-use';
import Introduction from '@betternet/shared/content/components/caa/caa-introduction';
import ContentCouponsNotification from '@betternet/shared/content/components/coupons/content-coupons-notification';
import ContentCouponsList from '@betternet/shared/content/components/coupons/content-coupons-list';
import { sendContentMessage } from '@softomate/rework-party';

function ContentCouponsController({ offers }) {
  const {
    isShoppingFeatureAccepted,
    onShoppingFeatureAccept,
    onShoppingFeatureClose,
    isShoppingFeatureClosed
  } = useAcceptSafeShoppingFeature();
  const [closeCount, setCloseCount] = useSessionStorage('betternent_caa_close', 0);
  const [hide, setHide] = useState(() => !/(checkout|cart|payment|bag|buy|orders)/i.test(window.location.pathname));
  const [showCoupons, setShowCoupons] = useState(false);
  useEffect(() => {
    sendContentReport('notification_coupons_list', 'show', window.location.hostname);
    sendContentInternalAnalytics('ui_view', { view_name: 'ntf_coupons' });
  }, []);

  const onIntroductionClose = useCallback(() => {
    setCloseCount(closeCount + 1);
    onShoppingFeatureClose(true);
  }, [closeCount]);

  const onCouponsClose = useCallback(() => {
    sendContentReport('notification_coupons_list', 'close', window.location.hostname);
    setCloseCount(closeCount + 1);
    setHide(true);
  }, [closeCount]);

  const onNotificationClose = useCallback(() => {
    setCloseCount(closeCount + 1);
    setHide(true);
  }, [closeCount]);

  const onNotificationAccept = useCallback(() => {
    sendContentMessage({ method: 'coupons.activations.activationMessageHandler' });
    setShowCoupons(true);
  }, [closeCount]);

  const domain = window.location.hostname;

  if (closeCount >= 2 || hide || (isShoppingFeatureAccepted === false && isShoppingFeatureClosed)) {
    return <div />;
  }
  if (isShoppingFeatureAccepted === false && !isShoppingFeatureClosed) {
    return <Introduction apply={onShoppingFeatureAccept} showLater={onIntroductionClose} />;
  }
  if (showCoupons) {
    return <ContentCouponsList offers={offers} domain={domain} close={onCouponsClose} />;
  }
  return (
    <ContentCouponsNotification
      count={offers.length}
      apply={onNotificationAccept}
      close={onNotificationClose}
      domain={domain}
    />
  );
}

export default ContentCouponsController;
