const promiseWrapper = (parent, method) => function () {
  const newArguments = [...arguments];
  return new Promise((resolve) => {
    newArguments.push(function () {
      if (chrome.runtime.lastError) {
        console.error(chrome.runtime.lastError.message);
      }
      return resolve(...arguments);
    });
    parent[method](...newArguments);
  });
};

const rx = /(.*:\/\/www(\w|\d)?\.|.*:\/\/)?([^/:]*).*/i;

export const getStorageData = promiseWrapper(chrome.storage.local, 'get');
export const setStorageData = promiseWrapper(chrome.storage.local, 'set');
export const removeStorageData = promiseWrapper(chrome.storage.local, 'remove');
export const getTabs = promiseWrapper(chrome.tabs, 'query');
export const createTab = promiseWrapper(chrome.tabs, 'create');
export const updateTab = promiseWrapper(chrome.tabs, 'update');
export const createWindow = promiseWrapper(chrome.windows, 'create');
export const removeTabs = promiseWrapper(chrome.tabs, 'remove');
export const getCurrentWindow = promiseWrapper(chrome.windows, 'getCurrent');
export const getCookies = promiseWrapper(chrome.cookies, 'getAll');
export const sendContentMessage = promiseWrapper(chrome.runtime, 'sendMessage');
export const sendBackgroundMessage = promiseWrapper(chrome.tabs, 'sendMessage');
export const asyncTimeout = async (t) => new Promise((r) => setTimeout(() => r(true), t));
export const checkPermissions = promiseWrapper(chrome.permissions, 'contains');

export const getAllTabs = () => getTabs({});

export const getActiveTab = () => getTabs({ active: true, currentWindow: true })
  .then(([tab]) => tab);

export const copyToClipBoard = (string) => {
  const input = document.createElement('input');
  input.style.position = 'absolute';
  document.body.appendChild(input);
  input.value = string;
  input.select();
  document.execCommand('Copy');
  document.body.removeChild(input);
};

export const extractSubdomainFromUrl = (url) => url && url.replace(rx, '$3').toLowerCase();
