import RulesGenerator from '../RulesGenerator';
import { log, requireCache, waitNodes } from '../utils';
import StepBase from './StepBase';
import { CouponStepNames } from '../enums';

const { UNLOCK } = CouponStepNames;

export default class Unlock extends StepBase {
  constructor(arg) {
    super(arg);
    this.setSteps([UNLOCK]);
  }

  async [UNLOCK]() {
    const { unlock } = this.config;

    if (!unlock) {
      throw new Error('no unlock config');
    }

    const rules = new RulesGenerator({
      config: this.config,
      rules: unlock,
    });

    let result;
    /* eslint-disable */
    for await (const rule /** @type {Rule}*/ of rules) {
      // check if unlock is required

      const {waitFor, waitUntil} = rule;
      if (waitFor || waitUntil) {
        const isAlreadyValid = waitNodes({waitFor, waitUntil});
        if (!isAlreadyValid) {
          result = await this.action(rule, requireCache(this.config));
        }
      } else {
        try {
          result = await this.action(rule, requireCache(this.config));
        } catch (e) {
          log('log', 'error unlocking', e);
        }
      }
    }
    /* eslint-enable */

    return result;
  }
}
