/**
 * @typedef {Object} AppliedCoupon
 * @property {string} code
 * @property {number} discount
 * @property {boolean} success -- true if if validation rules exist and result passed validation
 *      or if there is no validation rules and total has decreased.
 * @property {boolean} wasValidated -- true if validation rules exist and result passed validation.
 */

/**
 * @typedef {Object} AppliedCoupons
 * @property {[AppliedCoupon]} state
 * @property {number} total
 */

/**
 * @type AppliedCoupons
 */
export default class AppliedCoupons {
  /**
   * @param total {number} -- price without any coupons
   * @param cache {[object]?} -- saved result of coupons applying in case of page reloading
   */
  constructor(total, cache = []) {
    this.state = cache.map((el) => {
      const result = el;
      result.discount = Number(el.discount);
      return result;
    });
    this.total = total;
  }

  /**
   * @private
   * @param el {AppliedCoupon}
   */
  push(el) {
    this.state.push(el);
  }

  /**
   * @param results {[AppliedCoupon]}
   */
  pushStack(results) {
    this.state = this.state.concat(results);
  }

  /**
   * @private
   * @param newTotal
   *
   * recalculate discount for validated applied coupons
   */
  resetTotal(newTotal) {
    const totalDiff = newTotal - this.total;
    this.state = this.state.map((el) => {
      const result = el;
      if (el.wasValidated) {
        result.discount = el.discount + totalDiff;
      }
      return result;
    });
  }

  /**
   * @private
   * @return {AppliedCoupon[]}
   */
  getState() {
    return this.state;
  }
}
